const sjcl = require('sjcl');
const Hash = require('ipfs-only-hash');
const bs58 = require('bs58');
const { Base64 } = require('js-base64');
const generator = require('generate-password');
import { sdkWallet } from "../secrets/sdkHandler";
import { picLink } from "../secrets/secrets";
const BN = require("bn.js");

export const getEnDict = () => {
    return ["iv", "salt", "ct"];
};

export const getBaseDict = () => {
    return { "v": 1, "iter": 10000, "ks": 128, "ts": 64, "mode": "ccm", "adata": "", "cipher": "aes" };
};

export const makeHash = {
    // input content and output cid
    hash: async(data) => {
        const q = (await Hash.of(data));
        return q;
    },
    // convert cid to hex hash
    qh: (q) => {
        const m = bs58.decode(q);
        const h = m.toString('hex').slice(4, );
        return h;
    },
    // convert hex hash to cid
    hq: (h) => {
        const b = Buffer.from('1220' + h, 'hex');
        const q = bs58.encode(b);
        return q;
    }
};

export const sdkEncoder = {

        // convert memo hash to cid format
        MemoToCid: (txt) => {
            let buffer = Buffer.from(txt, 'base64');
            let string = buffer.toString('hex');
            return makeHash.hq(string);
        },

        // convert cid to memo hash (for checking only, use makeHash.qh(cid) instead)
        CidToMemoHash: (txt) => {
            txt = makeHash.qh(txt);
            txt = Buffer.from(txt, 'hex');
            return Base64.encode(txt);
        },

        // convert xid to zid
        xidTozid: (code, pre = 'CB') => {
            return Base64.encode(`${pre}${code.slice(1,)}`).slice(3, );
        },

        // convert zid to xid
        zidToxid: (code, isSeed = false, pre = 'Q0J') => {
                let mode = 'G';
                if (isSeed) {
                    mode = 'S';
                };
                return `${mode}${Base64.decode(`${pre}${code}`).slice(2,)}`;
    },
}

export const Encrypt = async (message, key = '', refreshRate = 0) => {
    const enDict = getEnDict();
    var encrypted = {};
    try {
        if (refreshRate > 0) {
            key = key.toString() + Math.floor(Date.now() / (1000 * refreshRate)).toString();
        };
        if (typeof message == 'object') {
            message = JSON.stringify(message);
        };
        const encryptedObj = sjcl.encrypt(key, message);
        let encryptedJSON = await JSON.parse(encryptedObj);
        enDict.forEach(i => {
            encrypted[i] = encryptedJSON[i];
        });
        return Base64.encode(JSON.stringify(encrypted));
    } catch (err) {
        return JSON.stringify(encrypted);
    };
};

export const Decrypt = (encrypted, key = '', refreshRate = 0) => {
    var message = "";
    try {
        if (refreshRate > 0) {
            key = key.toString() + Math.floor(Date.now() / (1000 * refreshRate)).toString();
        };
        message = sjcl.decrypt(key, Base64.decode(encrypted.toString()));
        return message;
    } catch (err) {
        return message;
    };
};

export const objB64 = {
    prefix: picLink,
    suffix: '.png',
    encode: (objInput, url = false) => {
        let code = Base64.encode(JSON.stringify(objInput));
        if (url) {
            code = `${objB64.prefix}${code}${objB64.suffix}`;
        };
        return code;
    },
    decode: async (code, url = false) => {
        if (url) {
            code = code.split("/userpic/")[1].slice(0, -objB64.suffix.length);
        };
        const decode = await JSON.parse(Base64.decode(code));
        return decode;
    },
}

export const edCoder = {
    baseTxt: "0123456789ABCEDFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz",
    encode: (numText) => {
        const l = new BN(edCoder.baseTxt.length);
        var code = "";
        var num = new BN(numText);
        while (!num.isZero()) {
            let rem = num.divmod(l).mod;
            rem = rem.toString();
            rem = Number(rem);
            code = edCoder.baseTxt[rem] + code;
            num = num.div(l);
        };
        return code;
    },
    decode: (encodedTxt) => {
        const l = new BN(edCoder.baseTxt.length);
        var num = new BN(0);
        var s = 0;
        var pass = true;
        for (var i = 0; i < encodedTxt.length; i++) {
            s = edCoder.baseTxt.indexOf(encodedTxt[encodedTxt.length - i - 1]);
            if (s == -1) {
                pass = false;
            };
            let bigS = new BN(s);
            let p = new BN(i);
            num = num.add(bigS.mul(l.pow(p)));
        };
        if (!pass) {
            num = 0;
        };
        return num.toString();
    },
};

export const getPassword = (n = 16, numbers = true, symbols = false) => {
    let password = generator.generate({
        length: n,
        numbers,
        symbols,
    });
    return password;
};

export const getMnemonic = (words = 24) => {
    let args = {};
    switch (words) {
        case 21:
            args = { entropyBits: 224 };
            break;
        case 18:
            args = { entropyBits: 160 };
            break;
        case 12:
            args = { entropyBits: 128 };
            break;
    };
    const mnemonic = sdkWallet.generateMnemonic(args);
    return mnemonic;
};

export const validateMnemonic = (mnemonic) => {
    return sdkWallet.validateMnemonic(mnemonic); // false
};

export const getAccount = (mnemonic, entropy = "", n = 0) => {
    const wallet = sdkWallet.fromMnemonic(mnemonic, entropy);
    const xid = wallet.getPublicKey(n);
    const seed = wallet.getSecret(n);
    const account = {
        xid,
        seed
    };
    return account;
};