<template>
  <q-layout view="lHr LpR lFf">
    <q-header class="bg-primary text-white">
      <q-toolbar>
        <q-btn dense flat round icon="menu" @click="toggleLeftDrawer" />

        <q-toolbar-title class="text-weight-bold" style="max-width: 700px">
          <span class="gt-sm">{{ $route.name }}</span>
          <q-icon
            name="fas fa-bomb"
            size="md"
            color="white"
            class="header-icon q-pa-sm lt-md"
          />
        </q-toolbar-title>
      </q-toolbar>
    </q-header>

    <q-drawer show-if-above v-model="leftDrawerOpen" side="left" bordered>
      <q-icon name="fas fa-bomb" size="lg" color="primary" class="q-pa-md" />
      <q-list>
        <q-item
          to="/"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="account_balance_wallet" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">Account</q-item-section>
        </q-item>

        <q-item
          v-if="zid && isInstalled && !needRestore && !isLocked"
          to="/collectables"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="fas fa-cubes" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">Collectables</q-item-section>
        </q-item>

        <q-item
          to="/nfts"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="token" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">NFTs</q-item-section>
        </q-item>

        <q-item
          v-if="zid && isInstalled && !needRestore && !isLocked"
          to="/trade"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="volunteer_activism" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">Trade</q-item-section>
        </q-item>

        <!-- <q-item
          v-if="zid && isInstalled && !needRestore && !isLocked"
          to="/shop"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="gavel" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">Shop</q-item-section>
        </q-item> -->

        <q-item
          v-if="zid && isInstalled && !needRestore && !isLocked"
          to="/settings"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="construction" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">Settings</q-item-section>
        </q-item>

        <q-item
          to="/about"
          :replace="displayMode == 'standalone'"
          clickable
          v-ripple
          exact
        >
          <q-item-section avatar>
            <q-icon name="info" size="md" />
          </q-item-section>
          <q-item-section class="text-h6">About</q-item-section>
        </q-item>
      </q-list>
    </q-drawer>

    <q-page-container>
      <q-page class="q-pa-md">
        <div class="q-gutter-y-md column" style="max-width: 700px">
          <router-view @reload="loadData" />
        </div>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { ref } from "vue";
import getUser from "./composables/getUser";
import { readWallet, getPWADisplayMode } from "./composables/handlers";
import { iconSet } from "./constants/iconSet";
import * as K from "./constants/constants";

export default {
  name: "LayoutDefault",
  components: {},
  setup() {
    const info = ref(null);
    const displayMode = ref(getPWADisplayMode());
    const leftDrawerOpen = ref(false);
    const isInstalled = ref(false);
    const needRestore = ref(true);
    const isLocked = ref(false);

    const toggleLeftDrawer = () => {
      leftDrawerOpen.value = !leftDrawerOpen.value;
    };
    const { user } = getUser();
    const zid = ref(null);

    const loadData = async () => {
      const wallet = await readWallet();
      if (wallet.secure) {
        if (wallet.secure.mnemonic) {
          needRestore.value = false;
        } else {
          needRestore.value = true;
        }
        if (wallet.secure.locked) {
          isLocked.value = true;
        } else {
          isLocked.value = false;
        }
      }
      const appStatus = getPWADisplayMode();
      if (appStatus == "standalone" || (process.env.NODE_ENV != "production" && K.devMode)) {
        isInstalled.value = true;
      }

      try {
        zid.value = wallet.disclose.zid;
      } catch {
        zid.value = null;
      }
    };
    loadData();

    return {
      leftDrawerOpen,
      displayMode,
      isInstalled,
      needRestore,
      isLocked,
      toggleLeftDrawer,
      user,
      zid,
      loadData,
      info,
      iconSet,
    };
  },
};
</script>

