<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Settings 設定</h4>
    <Options />
    <AppButton text="Update 更新" :action="updateApp" color="green" />
    <AppButton text="Logout 登出" :action="logoutHandler" color="red" />
    <span class="versionTxt">Version: {{K.version}}</span>
  </div>
</template>

<script>
import { useRouter } from "vue-router";
import useLogout from "../composables/useLogout";
import Options from "../components/Options.vue";
import AppButton from "../components/AppButton.vue";
import CreateWallet from "../components/CreateWallet.vue";
import * as K from "../constants/constants";
import { dialogHandler, storage } from "../composables/handlers";

export default {
  name: "Settings",
  components: { Options, CreateWallet, AppButton },
  emits: ["reload"],
  setup(props, context) {
    const router = useRouter();

    const { logout, outError } = useLogout();

    const logoutHandler = async () => {
      dialogHandler({
        title: "Confirm Logout 確認登出?",
        message:
          "All local storage will be cleared upon logging out, please make sure your mnemonic is backed up!<br>登出後會自動洗機，請確認你已經抄低密䃙。",
        persistent: true,
        html: true,
        ok: {
          label: "Confirm 確認",
          style: { "text-transform": "none" },
          color: "red",
        },
        cancel: {
          label: "Cancel 取消",
          style: { "text-transform": "none" },
        },
      }).onOk(async () => {
        router.replace({ name: "Account" });
        await logout();
        if (!outError.value) {
          K.print("user logged out");
        }
      });
    };

    const reloadInfo = async () => {
      context.emit("reload");
    };

    const updateApp = async () => {
      dialogHandler({
        title: "Confirm Update 確認更新?",
        message:
          "Please close and restart the app after update.<br>更新後請閂咗再開返個app。",
        persistent: true,
        ok: {
          label: "Understand 明",
          style: { "text-transform": "none" },
          color: "green",
        },
        cancel: {
          label: "Cancel 取消",
          style: { "text-transform": "none" },
        },
        html: true,
      }).onOk(async () => {
        await router.replace({ name: "Account" });
        await storage.remove("colContent");
        await storage.remove("colIndex");
        await storage.remove("nftContent");
        await storage.remove("nftIndex");
        await storage.remove("account");
        await storage.remove("balances");
        await storage.remove("myBids");
        window.location.reload(true);
      });
    };

    return {
      logoutHandler,
      reloadInfo,
      K,
      updateApp,
    };
  },
};
</script>

<style scoped>
.bottom {
  margin-top: 30px;
}
.versionTxt {
  color: grey;
}
</style>