import * as K from "../constants/constants";
import * as SH from "../secrets/sdkHandler";
import { assetDefinition } from '../secrets/secrets';
import { readWallet } from "../composables/handlers"
import { sdkEncoder } from './cipherHandler';

const sdk = SH.sdk;
const networkPassphrase = SH.networkPassphrase;
const server = SH.newServer();

export const actions = { create: 'create', payment: 'payment', claimable: 'claimable', claim: 'claim', trust: 'trust', data: 'data' }
export const trustLimit = { MAX: "922337203685.4775807", MIN: "0.0000001", NIL: "0", POS: "50000" };
export const issue_zid = assetDefinition[K.asset].asset_issuer;
export const distributor_zid = assetDefinition[K.asset].asset_distributor;
export const auctioneer_zid = assetDefinition.auctioneer;
export const bookkeeper_zid = assetDefinition.bookkeeper;
export const nftmanager_zid = assetDefinition.nftmanager;
export const nftissuer_zid = assetDefinition.nftissuer;

export const Operations = async(ops, send_memo) => {
    let err = null;
    let trans = [];
    try {
        const wallet = await readWallet()
        let source_secret = sdkEncoder.zidToxid(wallet.secure.zeed, true);
        const issue_xid = sdkEncoder.zidToxid(issue_zid);

        const GAS = sdk.Asset.native();
        var sourceKeys = sdk.Keypair.fromSecret(source_secret);
        const sourceAccount = await server.loadAccount(sourceKeys.publicKey());
        const fee = await SH.fee();
        const memoTxt = send_memo;

        let transaction = new sdk.TransactionBuilder(sourceAccount, {
                fee,
                networkPassphrase,
            }).addMemo(sdk.Memo.text(memoTxt))
            .setTimeout(SH.timeout)

        ops.map((op) => {
            let addedme = false;
            let muxing = false;
            if (op.muxpin) {
                muxing = true;
            }
            let destination = op.xid;
            if (op.action == actions.create ||
                op.action == actions.payment ||
                op.action == actions.claimable ||
                op.action == actions.trust) {
                try {
                    destination = sdkEncoder.zidToxid(destination);
                    K.print("in operations destination");
                    sdk.Keypair.fromPublicKey(
                        destination,
                    );
                } catch {
                    let message = 'Invalid destionation address.'
                    K.error(message)
                    throw new Error(message);
                };
            };
            const amount = op.amount;
            let ASSET = GAS;
            if (op.action != actions.data && op.action != actions.claim && op.asset_code.length <= 12 && op.asset_code != K.gas) {
                ASSET = new sdk.Asset(op.asset_code, issue_xid); // as default asset.
            };
            try {
                if (op.action == actions.payment) {
                    if (op.asset_code == K.asset || op.asset_code == K.gas) {
                        if (op.asset_code == K.asset && destination == issue_xid) {
                            const message = 'Cannot send to asset issuer.';
                            K.error(message);
                            throw new Error(message);
                        } else {
                            if (sourceKeys.publicKey() != destination) {
                                try {
                                    if (muxing) {
                                        const muxed = new sdk.MuxedAccount(new sdk.Account(destination, '0'), op.muxpin.toString());
                                        destination = muxed.accountId();
                                    }
                                    transaction.addOperation(sdk.Operation.payment({
                                        destination,
                                        asset: ASSET,
                                        amount,
                                        withMuxing: muxing,
                                    }));
                                    addedme = true;
                                    trans.push(op);
                                } catch (e) {
                                    K.error(e.message)
                                }

                            } else {
                                const message = 'Cannot send asset to oneself.';
                                K.error(message);
                                throw new Error(message);
                            };
                        };
                    } else {
                        const message = 'Invalid payment of asset.';
                        K.error(message);
                        throw new Error(message);
                    };
                } else if (op.action == actions.create) {
                    transaction.addOperation(sdk.Operation.createAccount({
                        destination,
                        startingBalance: amount // gas
                    }));
                    addedme = true;
                    trans.push(op);
                } else if (op.action == actions.claimable) {
                    const asset_codes = op.asset_code.split(":");
                    if (asset_codes.length == 2) {
                        if (asset_codes[0] != K.asset) {
                            try {
                                const pair = sdk.Keypair.fromPublicKey(
                                    sdkEncoder.zidToxid(asset_codes[1]),
                                );
                                if (destination != pair.publicKey()) {
                                    if (asset_codes[0].length <= 12) {
                                        ASSET = new sdk.Asset(asset_codes[0], pair.publicKey());
                                    } else {
                                        const message = 'Invalid NFT asset code.';
                                        K.error(message);
                                        throw new Error(message);
                                    };
                                } else {
                                    const message = 'Cannot send NFT to issuer.';
                                    K.error(message);
                                    throw new Error(message);
                                };
                            } catch {
                                const message = 'Invalid NFT asset issuer ID.';
                                K.error(message);
                                throw new Error(message);
                            };
                        } else {
                            ASSET = new sdk.Asset(asset_codes[0], issue_xid);
                        };
                    } else if (asset_codes.length == 1) {
                        if (asset_codes[0] != K.asset && asset_codes[0] != K.gas) {
                            const message = 'Invalid asset code.';
                            K.error(message);
                            throw new Error(message);
                        };
                    } else if (asset_codes.length > 2) {
                        const message = 'Too many asset attributes.';
                        K.error(message);
                        throw new Error(message);
                    };

                    let passTime = 0;
                    if (op.passTime) {
                        passTime = op.passTime;
                    };
                    let soon = (Math.ceil((Date.now() / 1000) + passTime)).toString();
                    let beforeDeadline = sdk.Claimant.predicateBeforeAbsoluteTime(soon);
                    let afterDeadline = sdk.Claimant.predicateNot(
                        beforeDeadline
                    );
                    let claimants = [
                        new sdk.Claimant(destination, afterDeadline),
                        new sdk.Claimant(sourceKeys.publicKey(), afterDeadline),
                    ];
                    if (op.claimants) {
                        claimants = op.claimants;
                    };
                    try {
                        transaction.addOperation(sdk.Operation.createClaimableBalance({
                            claimants,
                            asset: ASSET,
                            amount,
                        }));
                        addedme = true;
                        trans.push(op);
                    } catch {
                        const message = 'Invalid claimants setup.';
                        K.error(message);
                        throw new Error(message);
                    };
                } else if (op.action == actions.trust) {
                    let limit = amount;
                    switch (amount) {
                        case "MAX":
                            limit = trustLimit.MAX;
                            break;
                        case "MIN":
                            limit = trustLimit.MIN;
                            break;
                        case "NIL":
                            limit = trustLimit.NIL;
                            break;
                        default:
                            limit = amount
                    };
                    ASSET = new sdk.Asset(op.asset_code, destination)
                    transaction.addOperation(
                        sdk.Operation.changeTrust({
                            asset: ASSET,
                            limit
                        }),
                    );
                    addedme = true;
                    trans.push(op);
                } else if (op.action == actions.data) {
                    transaction.addOperation(
                        sdk.Operation.manageData({
                            name: op.asset_code,
                            value: op.amount
                        }),
                    );
                    addedme = true;
                    trans.push(op);
                } else if (op.action == actions.claim) {
                    const balanceId = destination;
                    let claimBalance = sdk.Operation.claimClaimableBalance({ balanceId });
                    transaction.addOperation(claimBalance);
                    addedme = true;
                    trans.push(op);
                };
                if (!addedme) {
                    let message = 'This operation is not added.';
                    throw new Error(message);
                };
            } catch (e) {
                err = e.message;
                return err;
            };
        });
        if (ops.length == trans.length) {
            const tx = transaction.build();
            tx.sign(sourceKeys);
            try {
                await server.submitTransaction(tx);
                K.print('Successful transaction!')
                return true;
            } catch (e) {
                err = e.response.data.extras.result_codes;
                K.error(err);
                return err;
            };
        } else {
            throw new Error('Not all operations are added successfully.');
        };
    } catch (e) {
        err = e.message;
        K.error(err)
        return err;
    };
};