<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Collectables 收藏</h4>
    <div v-if="!isLoaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>

    <div v-else>
      <div v-if="assets.length > 0">
        <q-pull-to-refresh @refresh="refresh">
          <q-list>
            <q-item
              v-for="a in assets"
              :key="HtmlSanitizer.SanitizeHtml(a.asset_code)"
            >
              <div class="q-px-none q-pb-sm row items-start q-gutter-md">
                <q-card class="my-card">
                  <q-img
                    :src="HtmlSanitizer.SanitizeHtml(a.thumbnail)"
                    @click="openCard(a)"
                  >
                    <div class="absolute-top text-subtitle2 text-right">
                      <span>Collectables 收藏</span>
                    </div>
                  </q-img>

                  <q-card-section>
                    <div
                      class="text-h6"
                      :class="{
                        forcewrap: wrapTitle(
                          HtmlSanitizer.SanitizeHtml(a.name)
                        ),
                      }"
                    >
                      {{ HtmlSanitizer.SanitizeHtml(a.name) }}
                    </div>
                    <div class="text-subtitle2">
                      LOT {{ HtmlSanitizer.SanitizeHtml(a.lot) }} of
                      {{ HtmlSanitizer.SanitizeHtml(a.limit) }}
                    </div>
                  </q-card-section>

                  <q-card-section class="q-pt-none forcewrap">
                    {{ `ipfs://${HtmlSanitizer.SanitizeHtml(a.cid)}` }}
                  </q-card-section>
                  <AppButton text="Open 開" :action="openCard" :args="a" />
                </q-card>
              </div>
            </q-item>
          </q-list>
        </q-pull-to-refresh>
      </div>
      <div v-else>
        <p class="introText">
          Welcome to the Collectables page! You can trade NFTs from the
          exchange.
        </p>
        <p>歡迎嚟到收藏。請先交換良心代幣</p>
        <AppButton text="Go 去" :action="toNfts" />
      </div>
    </div>
  </div>
  <q-dialog v-model="card" persistent>
    <q-card class="my-card">
      <q-card-section style="max-height: 100vh" class="q-pt-sm scroll">
        <a
          :href="getCidLinks(selected.image.replace('ipfs://', '')).link1"
          target="_blank"
        >
          <q-img :src="selected.thumbnail">
            <div
              class="absolute-top text-subtitle2 text-center"
              :class="{ forcewrap: wrapTitle(selected.name) }"
            >
              {{ selected.name }}
            </div>
          </q-img>
        </a>

        <div class="text-subtitle2">
          LOT {{ selected.lot }} of {{ selected.limit }}
        </div>

        <div
          class="text-subtitle1"
          :class="{ forcewrap: wrapTitle(selected.description) }"
        >
          {{ selected.description }}
        </div>

        <div class="text-caption text-grey forcewrap">
          {{ `ipfs://${selected.cid}(${selected.asset_code})` }}
        </div>

        <q-separator />
        <div>
          <q-item-label header> </q-item-label>
        </div>
        <div class="text-subtitle1">
          <!-- <q-item-label header> </q-item-label> -->
        </div>

        <div
          v-if="!isPaying"
          class="row q-mb-md q-pb-md justify-between q-px-lg"
        >
          <a
            :href="getCidLinks(selected.image.replace('ipfs://', '')).link1"
            target="_blank"
          >
            <AppButton text="Appreciate 欣賞" :action="() => {}" />
          </a>
          <q-btn icon="close" flat rounded dense v-close-popup />
        </div>
        <div v-else style="text-align: center" class="q-pa-md q-mb-md">
          <q-spinner-cube color="primary" size="2.5rem" />
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import getAccount from "../composables/readAccount";
var axios = require("axios");
import * as K from "../constants/constants";
import { link } from "../secrets/sdkHandler";
import { assetDefinition } from "../secrets/secrets";
import {
  sdkEncoder,
  edCoder,
  Encrypt,
  Decrypt,
} from "../composables/cipherHandler";
import {
  nftissuer_zid,
  trustLimit,
  issue_zid,
} from "../composables/sdkOperations";
import {
  getCidLinks,
  flashThumbnail,
  storage,
  readWallet,
  getPWADisplayMode,
  countDecimals,
  assetClaimHandler,
} from "../composables/handlers";
import { ReadClaimable } from "../composables/readClaimable";
import { dataLink } from "../secrets/secrets";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const isLoaded = ref(false);
    const numberOfEntries = ref(0);
    const assets = ref([]);
    const hasNetwork = ref(true);
    const needRestore = ref(true);
    const card = ref(false);
    const selected = ref({});
    const isPaying = ref(false);
    const { accounts, fetchAccount, records } = getAccount();

    const loadData = async () => {
      numberOfEntries.value = window.history.length;
      if (getPWADisplayMode() == "standalone" && numberOfEntries.value >= 2) {
        router.go(numberOfEntries.value - 1);
      }
      const wallet = await readWallet();
      const key = wallet.pack.key;

      if (window.navigator.onLine) {
        try {
          let trans = [];
          await assetClaimHandler(trans);
          const hasResult = await fetchAccount();
          let myClaimable = await ReadClaimable("", "", true, 50);
          myClaimable = myClaimable.filter((e) => e.asset != "native");
          let addNft = myClaimable.map((e) => {
            let asset_code = "GAS";
            let asset_issuer = "native";
            if (e.asset != "native") {
              [asset_code, asset_issuer] = e.asset.split(":");
              asset_issuer = sdkEncoder.xidTozid(asset_issuer);
            }
            const alias = "";
            const amount = e.amount;
            let nft = true;
            let precision = 7;
            let limit = 0;
            if (
              (asset_code == K.asset && asset_issuer == issue_zid) ||
              asset_code == "native"
            ) {
              nft = false;
              precision = assetDefinition[K.asset].precision;
              limit = trustLimit.MAX;
            } else {
              limit = trustLimit.MIN;
            }

            if (
              sdkEncoder.xidTozid(e.sponsor) == wallet.disclose.zid &&
              asset_issuer == nftissuer_zid &&
              nft
            ) {
              return {
                alias,
                amount,
                asset_code,
                asset_issuer,
                limit,
                nft,
                precision,
              };
            }
          });
          addNft = addNft.filter((e) => {
            try {
              return e.nft == true;
            } catch {
              return false;
            }
          });
          if (hasResult) {
            let nfts = accounts.value.filter(
              (account) =>
                account.nft &&
                account.asset_issuer == nftissuer_zid &&
                Number(account.amount) >= Number(trustLimit.MIN) &&
                account.asset_code != K.gas
            );

            nfts = [...addNft, ...nfts];
            let colIndex;
            try {
              colIndex = Decrypt(await storage.get("colIndex"), key);
            } catch {
              colIndex = [];
            }
            let colContent;
            try {
              colContent = Decrypt(await storage.get("colContent"), key);
              colContent = await JSON.parse(colContent);
            } catch {
              colContent = [];
            }

            if (
              colIndex == JSON.stringify(nfts) &&
              nfts.length == colContent.length
            ) {
              assets.value = colContent;
              await storage.set("colContent", await Encrypt(assets.value, key));
            } else {
              await storage.set("colIndex", await Encrypt(nfts, key));
              assets.value = [];
              isLoaded.value = false;
              const direction = { asc: "asc", desc: "desc" };
              await Promise.all(
                nfts.map(async (nft) => {
                  let existing = colContent.filter((col) => {
                    return col.asset_code == nft.asset_code;
                  });
                  if (existing.length == 1) {
                    assets.value.push(existing[0]);
                  } else {
                    const limit = 1;
                    const xid = sdkEncoder.zidToxid(nft.asset_issuer);
                    const cursor = edCoder.decode(nft.asset_code);
                    const url = `${link}/accounts/${xid}/transactions?cursor=${cursor}&order=${direction.asc}&limit=${limit}&include_failed=false`;
                    let response = await axios.get(url);
                    let responseJson = response.data;
                    let records = responseJson._embedded.records;
                    await Promise.all(
                      records.map(async (record) => {
                        if (
                          record.memo_type == "hash" &&
                          sdkEncoder.xidTozid(record.source_account) ==
                            nftissuer_zid
                        ) {
                          const urlOps = `${link}/transactions/${record.id}/operations`;
                          let res = await axios.get(urlOps);
                          let resJson = res.data;
                          let ops = resJson._embedded.records;
                          if (
                            ops.length == 1 &&
                            ops[0].amount == trustLimit.MIN &&
                            ops[0].type == "create_claimable_balance"
                          ) {
                            const cid = sdkEncoder.MemoToCid(record.memo);
                            const metaLink = getCidLinks(cid);
                            const url = metaLink.link1;
                            const preUrl = `${dataLink}nfts/${
                              metaLink.link0.split("/ipfs/")[1]
                            }.json`;
                            let cidRes;
                            try {
                              cidRes = await axios.get(preUrl);
                            } catch {
                              cidRes = await axios.get(url);
                            };
                            let nftMeta = cidRes.data;
                            nftMeta["asset_code"] = nft.asset_code;
                            nftMeta["asset_issuer"] = nft.asset_issuer;
                            nftMeta["created_at"] = record.created_at;
                            nftMeta["thumbnail"] = await flashThumbnail(
                              nftMeta["thumbnail"]
                            );
                            nftMeta["mint_id"] = record.id;
                            nftMeta["cid"] = cid;
                            assets.value.push(nftMeta);
                          }
                        }
                      })
                    );
                    await storage.set(
                      "colContent",
                      await Encrypt(assets.value, key)
                    );
                  }
                })
              );
            }
          }
        } catch (err) {
          K.error(err.message);
        }
      } else {
        hasNetwork.value = false;
        const colContent = Decrypt(await storage.get("colContent"), key);
        try {
          assets.value = await JSON.parse(colContent);
        } catch {
          assets.value = [];
        }
      }
      assets.value.sort((a, b) => {
        if (a.name < b.name) {
          return -1;
        }
        if (a.name > b.name) {
          return 1;
        }
        if (a.lot < b.lot) {
          return -1;
        }
        if (a.lot > b.lot) {
          return 1;
        }
      });
      await storage.set("nftContent", await Encrypt(assets.value, key));
      isLoaded.value = true;
    };

    loadData();

    const openCard = (item) => {
      selected.value = item;
      card.value = true;
    };

    const toNfts = () => {
      router.replace({ name: "NFTs" });
    };

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    return {
      toNfts,
      getCidLinks,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
      assets,
      isLoaded,
      wrapTitle,
      HtmlSanitizer,
      openCard,
      needRestore,
      card,
      selected,
      isPaying,
      countDecimals,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

<style scoped>
.forcewrap {
  word-break: break-all;
}
.my-card {
  width: 100%;
  width: calc(90vw);
  max-width: 700px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
.infoText {
  font-size: 1rem;
  font-weight: bold;
  color: black;
}
.introText {
  font-size: 1.25rem;
}
a:link {
  text-decoration: none;
  color: black;
}

a:visited {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: none;
  color: black;
}

a:active {
  text-decoration: none;
  color: black;
}
</style>