import { ref } from 'vue';
import * as K from "../constants/constants";
import * as SH from "../secrets/sdkHandler";
import { notifyHandler } from "../composables/handlers";
import { sdkEncoder } from "../composables/cipherHandler";
// when using "@ledgerhq/hw-transport-node-hid" library you need to go to
// Settings -> Browser support in ledger stellar app and set this setting to 'No'
import Transport from "@ledgerhq/hw-transport-webhid";
// import Transport from "@ledgerhq/hw-transport-u2f"; // for browser
import Str from "@ledgerhq/hw-app-str";

const sdk = SH.sdk;
const networkPassphrase = SH.networkPassphrase;
const server = SH.newServer();

export const getStrAppVersion = async() => {
    try {
        const transport = await Transport.create();
        const str = new Str(transport);
        const result = await str.getAppConfiguration();
        return result.version;
    } catch (err) {
        K.error(err)
    }

};

export const getStrPublicKey = async() => {
    try {
        let listing = await Transport.list();
        if (listing.length > 0) {
            listing.forEach(async(list) => {
                await list.close();
            });
        };
        const transport = await Transport.create();
        const str = new Str(transport);
        const result = await str.getPublicKey("44'/148'/0'");
        const xid = result.publicKey;
        const zid = sdkEncoder.xidTozid(xid);
        K.print(`Address: ${zid}`);

        await transport.close();
        return result.publicKey;
    } catch (err) {
        K.error(err);
        notifyHandler({
            message: "Invalid connection. Please try again!<br>1. Connect and unlock your Ledger wallet <br> 2. Install and open the Stellar app",
            html: true,
            timeout: 8000,
            color: "warning",
        });
    }
};


export const signStrTransaction = async() => {
    const fee = await SH.fee();
    let publicKey = await getStrPublicKey();
    let muxing = false;

    const destination = "GCBQN6QIGOPDR6DXJUDPFSC7WJINXWQU2BJVS2V3A7OZLATV2FI6A7W4";
    const memoTxt = "note of memo";
    const amount = "1";
    const GAS = sdk.Asset.native();
    let ASSET = GAS;

    const sourceAccount = await server.loadAccount(publicKey);
    let transaction = new sdk.TransactionBuilder(sourceAccount, {
            fee,
            networkPassphrase,
        }).addMemo(sdk.Memo.text(memoTxt))
        .setTimeout(SH.timeout);
    transaction.addOperation(sdk.Operation.payment({
        destination,
        asset: ASSET,
        amount,
        withMuxing: muxing,
    }));
    const tx = transaction.build();
    const transport = await Transport.create();
    const str = new Str(transport);
    const result = await str.signTransaction("44'/148'/0'", tx.signatureBase());

    // add signature to transaction
    const keyPair = sdk.Keypair.fromPublicKey(publicKey);
    const hint = keyPair.signatureHint();
    const decorated = new sdk.xdr.DecoratedSignature({ hint: hint, signature: result.signature });
    tx.signatures.push(decorated);
    transport.close();
    return tx;
}

export const transactionToXDRstr = (transaction) => {
    let res = null;
    try {
        res = transaction.toEnvelope().toXDR().toString('base64');
    } catch {
        res = null;
    }
    return res
}

