import * as SH from "../secrets/sdkHandler";
import { assetDefinition } from '../secrets/secrets';
import { sdkEncoder } from "../composables/cipherHandler";

const sdk = SH.sdk;
const auctionXid = sdkEncoder.zidToxid(assetDefinition.auctioneer);
const bookXid = sdkEncoder.zidToxid(assetDefinition.bookkeeper);

const durations = {
    auction: 168, // allow 178 hours / 7 days for auctioning
    process: 2, // allow 2 hours for processing
    probation: 12, // allow 12 hours for undisturbed claimable collection
    submit: 0.25, // allow 15 mins for submit to happen
    stake: 0.1, // allow 6 mins for stake to differ
};

/*
let auction duration = 72 hrs and processing duration = 2 hrs.
preparation time is time before auction has ended: e.g. the 72 hrs auction period.
processing time is the time after auction has ended and before processing deadline: e.g. the 2 hrs server action period.
post-auction time is the time after processing time has ended: e.g. from the end of the 2 hrs server action period until end of time.

1. server send coin to user as claimable. (both unconditional: parallel; unconditional with probation: flow)
    [server and user] x2
    mode: parallel / flow
2. owner send NFT to server for auction as claimable. (owner cannot claim before and during processing, server can only claim during processing)
    [owner and server] x2
    mode: auction
3. bidder send coin for auction as claimable. (bidder cannot claim during processing, server can only claim during processing, owner was added but can only claim before an invalid past time)
    [bidder and server and owner] x3
    mode: bid
4. server send NFT to user as claimable. (both unconditional: parallel; unconditional with probation: flow)
    [server and user] x2
    mode: parallel / flow
5. user send NFT to user as claimable. (target user can claim unconditional, source user cannot claim, add cold server account unconditional as backup)
    [target user and cold server] x2
    mode: trade
6. user send coins to server as claimable. (server can claim before timeout, user can only claim after timeout.)
    mode: submit
7. user send NFT to server as claimable. (server can claim unconditional, user cannot claim with claim before creation time.)
    mode: stake
*/

export const modes = {
    flow: 'flow',
    parallel: 'parallel',
    auction: 'auction',
    bid: 'bid',
    trade: 'trade',
    submit: 'submit',
    stake: 'stake',
};

const getUnbound = () => {
    return sdk.Claimant.predicateUnconditional();
};

const getBefore = (soon) => {
    return sdk.Claimant.predicateBeforeAbsoluteTime(soon);
};

const getAfter = (soon) => {
    return sdk.Claimant.predicateNot(getBefore(soon));
};

const getOutside = (soon, soonAfter) => {
    let outside = sdk.Claimant.predicateOr(
        getBefore(soon),
        getAfter(soonAfter)
    );
    return outside;
};

const getWithin = (soon, soonAfter) => {
    let within = sdk.Claimant.predicateAnd(
        getAfter(soon),
        getBefore(soonAfter)
    );
    return within;
};

const getTimeNow = () => {
    return Math.ceil(Date.now() / 1000);
};

const getProbation = (duration = durations.probation) => {
    let later = 60 * 60 * duration;
    let timeNow = getTimeNow();
    let soon = (timeNow + later).toString();
    return getAfter(soon);
};

export const getClaimants = (sourcexid, targetxid, mode = "flow", processStart = "", processEnd = "", period = durations.submit) => { // period is 15 mins
    let claimants = null;
    if (mode == modes.flow) {
        let noDeadline = getUnbound();
        let afterDeadline = getProbation();
        claimants = [
            new sdk.Claimant(targetxid, noDeadline),
            new sdk.Claimant(sourcexid, afterDeadline),
        ];
    } else if (mode == modes.parallel) {
        let noDeadline = getUnbound();
        claimants = [
            new sdk.Claimant(targetxid, noDeadline),
            new sdk.Claimant(sourcexid, noDeadline),
        ];
    } else if (mode == modes.auction) {
        let auctionPeriod = 60 * 60 * durations.auction;
        let processPeriod = 60 * 60 * durations.process;
        let timeNow = getTimeNow();
        let soon = (timeNow + auctionPeriod).toString();
        let soonAfter = (timeNow + auctionPeriod + processPeriod).toString();
        let outside = getOutside(soon, soonAfter);
        let within = getWithin(soon, soonAfter);
        claimants = [
            new sdk.Claimant(targetxid, within),
            new sdk.Claimant(sourcexid, outside),
        ];
    } else if (mode == modes.bid) {
        let soon = processStart;
        let soonAfter = processEnd;
        let past = (getTimeNow() - 10).toString();
        let beforeDeadline = getBefore(past);
        let outside = getOutside(soon, soonAfter);
        let within = getWithin(soon, soonAfter);
        claimants = [
            new sdk.Claimant(auctionXid, within),
            new sdk.Claimant(sourcexid, outside),
            new sdk.Claimant(targetxid, beforeDeadline)
        ];
    } else if (mode == modes.trade) {
        let noDeadline = getUnbound();
        let afterDeadline = getProbation();
        claimants = [
            new sdk.Claimant(targetxid, noDeadline),
            new sdk.Claimant(bookXid, afterDeadline),
        ];
    } else if (mode == modes.submit) {
        let soon = (getTimeNow() + (period * 60 * 60)).toString();
        let beforeDeadline = getBefore(soon);
        let afterDeadline = getAfter(soon);
        claimants = [
            new sdk.Claimant(targetxid, beforeDeadline),
            new sdk.Claimant(sourcexid, afterDeadline),
        ];
    } else if (mode == modes.stake) {
        period = durations.stake;
        let soon = (getTimeNow() - (period * 60 * 60)).toString();
        let noDeadline = getUnbound();
        let beforeDeadline = getBefore(soon);
        claimants = [
            new sdk.Claimant(targetxid, noDeadline),
            new sdk.Claimant(sourcexid, beforeDeadline),
        ];
    };
    return claimants;
};