<template>
  <div v-if="!user">
    <!-- <button @click="loginHandler">Login with Google</button> -->
    <q-btn rounded color="blue" text-color="white" @click="loginHandler" no-caps>
      <div class="q-pa-sm">
        <q-icon left size="1.25rem" name="fab fa-google" />
        Login with Google 登入
      </div>
    </q-btn>
  </div>
  <div v-else>
    <AppButton text="Show user" :action="showUser" />
    <AppButton text="Logout" :action="logoutHandler" />
  </div>
</template>

<script>
import useLogout from "../composables/useLogout";
import useLogin from "../composables/useLogin";
import getUser from "../composables/getUser";
import AppButton from "./AppButton.vue";
import * as K from "../constants/constants";

export default {
  components: { AppButton },
  setup(props, context) {
    const { logout, outError } = useLogout();
    const { user } = getUser();
    const { error, login } = useLogin();

    const loginHandler = async () => {
      await login();
    };

    const showUser = () => {
      K.print("token:", user._rawValue._lat);
      K.print("uid:", user._value.uid);
      K.print("displayName:", user._value.displayName);
      K.print("url:", user._value.photoURL);
    };

    const logoutHandler = async () => {
      await logout();
      if (!outError.value) {
        K.print("user logged out");
      }
    };

    return {
      loginHandler,
      user,
      showUser,
      logoutHandler,
    };
  },
};
</script>

