<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Give 畀 {{K.asset_alias}}</h4>
    <div class="input-group">
      <div class="infoText" style="max-width: 700px">
        <q-input
          v-model="paymentCode"
          style="font-size: 1.25rem"
          @change="
            (newVal) => {
              paymentCode = newVal;
              checkValid();
            }
          "
          placeholder="Transaction Code 交易字串:"
          autogrow
        >
        <template v-slot:append>
          <q-icon name="close" @click="paymentCode = ''" class="cursor-pointer" />
        </template>
        </q-input>
      </div>
    </div>
  </div>

  <div class="q-my-5">
    <span v-if="isValid">
      <AppButton text="Proceed 去" :action="toPayment" />
    </span>
    <div class="q-my-md q-mb-lg text-center btnStyle">
      <q-btn filled rounded color="primary" @click="goScan" no-caps>
        <span>Scan 嘟 </span><q-icon class="q-mx-sm" name="qr_code_scanner" />
      </q-btn>
    </div>
    <AppButton text="Back 返去" :action="goBack" />
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import QRcode from "../components/QRcode.vue";
import AppButton from "../components/AppButton.vue";
const { Base64 } = require("js-base64");
import * as K from "../constants/constants";
import { transferPhrase } from "../secrets/secrets";
import { Decrypt } from "../composables/cipherHandler";

export default {
  components: { QRcode, AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const paymentCode = ref("");
    const paymentValid = ref(false);
    const isValid = ref(false);
    const transDetail = ref(null);

    const goBack = async () => {
      router.replace({ name: "Account" });
    };

    const goScan = async () => {
      router.replace({ name: "Scan" });
    };

    const checkValid = async () => {
      const content = paymentCode.value;
      if (paymentCode.value == "") {
        isValid.value = false;
      } else {
        try {
          transDetail.value = Decrypt(content, transferPhrase);
          const decoded = await JSON.parse(transDetail.value);
          if (
            decoded.type == "payment" &&
            decoded.asset_code == K.asset &&
            decoded.amount > 0 &&
            decoded.memo.length > 0 &&
            decoded.memo.length <= 28 &&
            decoded.name.length > 0 &&
            decoded.zid
          ) {
            isValid.value = true;
          } else {
            isValid.value = false;
          }
        } catch (err) {
          K.error(err.message);
          isValid.value = false;
        }
      }
    };

    const toPayment = async () => {
      router.replace({
        name: "Confirm",
        params: { decoded: transDetail.value },
      });
    };

    return {
      goBack,
      goScan,
      paymentValid,
      paymentCode,
      checkValid,
      toPayment,
      isValid,
      K,
    };
  },
};
</script>

<style scoped>
.infoText input {
  font-size: 1rem;
}
.btnStyle button {
  min-width: 40%;
  font-size: 1rem;
}
</style>