<template>
  <div v-if="!isLoaded" style="text-align: center">
    <q-spinner-grid color="primary" size="2.5rem" />
  </div>
  <div v-else>
    <div class="textwrap" v-html="markdownToHtml"></div>
    <div v-if="markdown == '' || !hasNetwork">
      <p>Nothing to see 冇嘢睇</p>
    </div>
    <div class="q-mt-xl">
      <AppButton text="Back 返去" :action="goBack" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
const axios = require("axios");
import marked from "marked";
import { postsLink } from "../constants/appLinks";

export default {
  components: { AppButton },
  emits: ["reload"],
  props: ["id", "back"],
  setup(props) {
    const router = useRouter();
    const hasNetwork = ref(true);
    const isLoaded = ref(false);
    const markdown = ref("");

    const goBack = async () => {
      let target = { name: "Posts" };
      try {
        target = await JSON.parse(props.back);
      } catch (err) {
        target = { name: "Posts" };
      }
      router.replace(target);
    };

    const loadData = async () => {
      const url = `${postsLink.split("index")[0]}posts/${props.id}.md`;
      if (window.navigator.onLine) {
        try {
          let resp = await axios.get(url);
          markdown.value = resp.data;
        } catch {
          markdown.value = "";
        }
      } else {
        hasNetwork.value = false;
      }
      isLoaded.value = true;
    };

    loadData();

    return {
      goBack,
      hasNetwork,
      isLoaded,
      markdown,
    };
  },
  computed: {
    markdownToHtml() {
      return HtmlSanitizer.SanitizeHtml(marked(this.markdown));
    },
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1.25rem;
}
p {
  white-space: pre-wrap;
}
.textwrap {
  max-width: 100vw
}
.forcewrap {
  word-break: break-all;
}
</style>