<template>
  <div
    :class="{ fullscreen: fullscreen }"
    ref="wrapper"
    @fullscreenchange="onFullscreenChange"
  >
    <qrcode-stream :camera="camera" @decode="onDecode" @init="onInit">
      <div class="loading-indicator" v-if="loading">
        <q-spinner-grid color="primary" size="2.5rem" />
      </div>
      <div class="q-ma-sm">
        <q-icon
          color="white"
          @click="goBack"
          name="fas fa-arrow-left"
          size="md"
        />
      </div>
      <div v-if="validationSuccess" class="validation-success">
        Proceeding to transaction!
      </div>

      <div v-if="validationFailure" class="validation-failure">
        Validation failed!
      </div>

      <div v-if="validationPending" class="validation-pending">
        Validation in progress...
      </div>
    </qrcode-stream>
    <div v-if="!loading"></div>
  </div>
</template>

<script>
import { QrcodeStream } from "vue3-qrcode-reader";
import { getPWADisplayMode } from "../composables/handlers";
import { useRouter } from "vue-router";
import * as K from "../constants/constants";
import { Decrypt } from "../composables/cipherHandler";
import { transferPhrase } from "../secrets/secrets";
const { Base64 } = require('js-base64');

export default {
  components: { QrcodeStream },

  data() {
    const selected = { text: "outline", value: this.paintOutline };
    const router = useRouter();
    return {
      isValid: undefined,
      camera: "auto",
      result: null,
      fullscreen: (() => {
        if (getPWADisplayMode() == "standalone") {
          return false;
        } else {
          return false;
        }
      })(),
      loading: false,
      selected,
      router,
    };
  },

  computed: {
    validationPending() {
      return this.isValid === undefined && this.camera === "off";
    },

    validationSuccess() {
      return this.isValid === true;
    },

    validationFailure() {
      return this.isValid === false;
    },
  },

  watch: {
    fullscreen(enterFullscreen) {
      if (enterFullscreen) {
        this.requestFullscreen();
      } else {
        this.exitFullscreen();
      }
    },
  },

  methods: {
    goBack() {
      this.turnCameraOff();
      this.router.replace({ name: "Give" });
    },

    onFullscreenChange(event) {
      // This becomes important when the user doesn't use the button to exit
      // fullscreen but hits ESC on desktop, pushes a physical back button on
      // mobile etc.

      this.fullscreen = document.fullscreenElement !== null;
    },

    requestFullscreen() {
      const elem = this.$refs.wrapper;

      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.mozRequestFullScreen) {
        /* Firefox */
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE/Edge */
        elem.msRequestFullscreen();
      }
    },

    exitFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) {
        /* Firefox */
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        /* Chrome, Safari and Opera */
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) {
        /* IE/Edge */
        document.msExitFullscreen();
      }
    },

    logErrors(promise) {
      promise.catch(console.error);
    },
    async onInit(promise) {
      this.loading = true;
      await promise.catch(console.error).then(this.resetValidationState);
      this.loading = false;
    },

    resetValidationState() {
      this.isValid = undefined;
    },

    async onDecode(content) {
      this.result = content;
      this.turnCameraOff();
      try {
        // const transDetail = Decrypt(content, transferPhrase);
        const transDetail = Base64.decode(content);
        const decoded = await JSON.parse(transDetail);
        K.print(decoded);
        await this.timeout(1000);
        // pretend it's taking really long
        if (
          decoded.type == "payment" &&
          decoded.asset_code == K.asset &&
          decoded.amount > 0 &&
          decoded.memo.length > 0 &&
          decoded.memo.length <= 28 &&
          decoded.name.length > 0 &&
          decoded.zid &&
          decoded.mode == !K.devMode
        ) {
          this.isValid = true;
          await this.router.replace({
            name: "Confirm",
            params: { decoded: transDetail },
          });
        } else {
          this.isValid = false;
        }
        // some more delay, so users have time to read the message
        await this.timeout(1000);
      } catch (err) {
        K.error(err.message);
      }
      this.turnCameraOn();
    },

    turnCameraOn() {
      this.camera = "auto";
    },

    turnCameraOff() {
      this.camera = "off";
    },

    timeout(ms) {
      return new Promise((resolve) => {
        window.setTimeout(resolve, ms);
      });
    },
  },

  beforeRouteLeave() {
    this.turnCameraOff();
  },
};
</script>

<style scoped>
.validation-success,
.validation-failure,
.validation-pending {
  position: absolute;
  width: 100%;
  height: 100%;
  color: grey;

  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 10px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.validation-success {
  color: green;
}

.validation-failure {
  color: black;
}

.fullscreen {
  position: fixed;
  z-index: 1000;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.fullscreen-button {
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  margin: 1rem;
}

.fullscreen-button img {
  width: 2rem;
}

.loading-indicator {
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
  margin-top: 20%;
  color: grey;
}
</style>
