<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Dashboard</h4>
    <p class="infoText">Welcome to the dashboard page!</p>
    <q-pull-to-refresh @refresh="refresh">
      <p>Issuer</p>
      <DashboardBalances :assets="issueBalance" />
      <p>Distributor</p>
      <DashboardBalances :assets="distBalance" />
      <p>Book Keeper</p>
      <DashboardBalances :assets="bookBalance" />
      <p>NFT Manager</p>
      <DashboardBalances :assets="nftManagerBalance" />
      <p>NFT Issuer</p>
      <DashboardBalances :assets="nftIssuerBalance" />
      <p>Auctioneer</p>
      <DashboardBalances :assets="auctioneerBalance" />
    </q-pull-to-refresh>
    <div>
      <AppButton text="Back 返去" :action="toAccount" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import DashboardBalances from "../components/DashboardBalances.vue";
import {
  issue_zid,
  distributor_zid,
  bookkeeper_zid,
  nftmanager_zid,
  nftissuer_zid,
  auctioneer_zid,
} from "../composables/sdkOperations";
import { readBalance } from "../composables/readBalance";
import * as K from "../constants/constants";

export default {
  components: { AppButton, DashboardBalances },
  setup() {
    const router = useRouter();

    const issueBalance = ref({});
    const distBalance = ref({});
    const bookBalance = ref({});
    const nftManagerBalance = ref({});
    const nftIssuerBalance = ref({});
    const auctioneerBalance = ref({});
    
    const loaded = ref(false);
    const hasNetwork = ref(true);

    const loadData = async () => {
      if (window.navigator.onLine) {
        issueBalance.value = await readBalance(issue_zid, [
          { asset_code: K.gas, asset_issuer: "native" },
        ]);
        distBalance.value = await readBalance(distributor_zid);
        bookBalance.value = await readBalance(bookkeeper_zid);
        nftManagerBalance.value = await readBalance(nftmanager_zid);
        nftIssuerBalance.value = await readBalance(nftissuer_zid);
        auctioneerBalance.value = await readBalance(auctioneer_zid);
      } else {
        hasNetwork.value = false;
      }
      loaded.value = true;
    };

    loadData();

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    return {
      toAccount,
      issueBalance,
      distBalance,
      bookBalance,
      nftManagerBalance,
      nftIssuerBalance,
      auctioneerBalance,
      loaded,
      hasNetwork,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
    };
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1.25rem;
}
</style>