<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Confirm {{ type }}</h4>

    <div v-if="isPaying" class="text-center">
      <q-spinner-cube color="primary" size="2.5rem" />
      <p>Connecting to blockchain...</p>
    </div>
    <div v-else>
      <div v-if="isValid">
        <div class="q-pa-none infoText" style="max-width: 700px">
          <q-list>
            <q-item>
              <q-item-section>
                <q-item-label>{{ name }}</q-item-label>
                <q-item-label class="forcewrap" caption
                  >ID: {{ zid }}</q-item-label
                >
              </q-item-section>
            </q-item>
            <q-item>
              <q-item-section>
                <q-item-label
                  >{{ asset_code }} <q-icon :name="iconSet[K.asset]" />
                  {{ amount }}</q-item-label
                >
              </q-item-section>
            </q-item>

            <q-item>
              <q-item-section>
                <q-item-label>Remarks:</q-item-label>
                <q-item-label class="forcewrap">{{ remarks }}</q-item-label>
              </q-item-section>
            </q-item>

            <q-item>
              <q-item-section>
                <q-item-label>Reference:</q-item-label>
                <q-item-label class="forcewrap" caption>{{
                  memo
                }}</q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </div>
        <AppButton text="Confirm 確定" :action="confirmPayment" />
      </div>
      <div v-else>
        <div v-if="!loaded" style="text-align: center">
          <q-spinner-grid color="primary" size="2.5rem" />
        </div>
        <div v-else>
          <p class="infoText">Transaction is not valid, please go back.</p>
        </div>
      </div>
      <div>
        <AppButton text="Back 返去" :action="toAccount" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import {
  readWallet,
  storage,
  notifyHandler,
  confirmPin,
} from "../composables/handlers";
import { sdkEncoder, Decrypt, Encrypt } from "../composables/cipherHandler";
import { sdk } from "../secrets/sdkHandler";
import * as K from "../constants/constants";
import { actions, trustLimit, Operations } from "../composables/sdkOperations";
import { iconSet } from "../constants/iconSet";
import getAccount from "../composables/readAccount";
const axios = require("axios");
import { dataLink } from "../secrets/secrets";

export default {
  name: "Confirm",
  components: { AppButton },
  props: ["decoded"],
  emits: ["reload"],
  setup(props) {
    const router = useRouter();
    const type = ref(null);
    const asset_code = ref(null);
    const amount = ref(null);
    const memo = ref(null);
    const remarks = ref(null);
    const name = ref(null);
    const zid = ref(null);
    const trans = ref([]);
    const isValid = ref(false);
    const loaded = ref(false);
    const isPaying = ref(false);
    const { accounts, fetchAccount, records } = getAccount();

    const validate = async () => {
      try {
        const wallet = await readWallet();
        const content = await JSON.parse(props.decoded);
        zid.value = content.zid;
        if (wallet.disclose.zid != zid.value) {
          try {
            try {
              let env = "";
              if (K.devMode) {
                env = "dev/";
              }
              const url = `${dataLink}${env}blocklist/${zid.value}.json`;
              let response = await axios.get(url);
              let responseJson = response.data;
              K.print(responseJson);
              let message = "Account is on blocklist.";
              K.error(message);
              throw new Error(message);
            } catch (err) {
              if (err.message == "Account is on blocklist.") {
                notifyHandler({
                  message: "Destination account is blocked!",
                  color: "negative",
                });
                throw new Error("Destination is blocked!");
              }
            }
            sdk.Keypair.fromPublicKey(sdkEncoder.zidToxid(zid.value));
          } catch (err) {
            K.error(err.message);
            loaded.value = true;
            // alert(`Destination is invalid!`);
            notifyHandler({
              message: "Destination is invalid!",
              color: "negative",
            });
            return false;
          }
        } else {
          loaded.value = true;
          // alert(`Cannot send asset to yourself!`);
          notifyHandler({
            message: "Cannot send asset to yourself!",
            color: "negative",
          });
          return false;
        }

        type.value = content.type;
        if (type.value != actions.payment) {
          loaded.value = true;
          // alert(`Invalid transaction type!`);
          notifyHandler({
            message: "Invalid transaction type!",
            color: "negative",
          });
          return false;
        }
        asset_code.value = content.asset_code;
        if (asset_code.value != K.asset) {
          loaded.value = true;
          // alert(`Invalid asset code!`);
          notifyHandler({
            message: "Invalid asset code!",
            color: "negative",
          });
          return false;
        }

        amount.value = content.amount;
        if (
          Number(amount.value) <= 0 ||
          Number(amount.value) > Number(trustLimit.MAX)
        ) {
          loaded.value = true;
          // alert(`Invalid transaction amount!`);
          notifyHandler({
            message: "Invalid transaction amount!",
            color: "negative",
          });
          return false;
        }

        memo.value = content.memo;
        if (memo.value.length <= 0 || memo.value.length > 28) {
          loaded.value = true;
          // alert(`Missing reference!`);
          notifyHandler({
            message: "Missing reference!",
            color: "negative",
          });
          return false;
        }

        remarks.value = content.remarks;
        if (remarks.value.length <= 0) {
          loaded.value = true;
          // alert(`Missing remarks!`);
          notifyHandler({
            message: "Missing remarks!",
            color: "negative",
          });
          return false;
        }

        name.value = content.name;
        if (!name.value) {
          loaded.value = true;
          // alert(`Missing username!`);
          notifyHandler({
            message: "Missing username!",
            color: "negative",
          });
          return false;
        }

        await fetchAccount();
        let account = await JSON.parse(
          Decrypt(await storage.get("account"), wallet.pack.key)
        );

        let baseAsset = account.balances.filter((e) => {
          return e.asset_code == K.asset;
        })[0];

        let baseGas = account.balances.filter((e) => {
          return e.asset_code == K.gas;
        })[0];

        if (Number(account.min_balance) > Number(baseGas.amount)) {
          loaded.value = true;
          // alert(`Insufficient GAS!`);
          notifyHandler({
            message: "Insufficient GAS!",
            color: "negative",
          });
          return false;
        }

        if (Number(amount.value) > Number(baseAsset.amount)) {
          loaded.value = true;
          // alert(`Insufficient asset amount!`);
          notifyHandler({
            message: "Insufficient asset amount!",
            color: "negative",
          });
          return false;
        }

        trans.value = [
          {
            action: actions.payment,
            xid: zid.value,
            asset_code: asset_code.value,
            amount: amount.value,
          },
        ];

        isValid.value = true;
      } catch (err) {
        K.error(err.message);
        loaded.value = true;
        return false;
      }
    };

    validate();

    const confirmPayment = async () => {
      const item = await JSON.parse(props.decoded);
      const wallet = await readWallet();

      const passFunc = async () => {
        isPaying.value = true;
        try {
          const res = await Operations(trans.value, memo.value);
          if (res == true) {
            isPaying.value = false;
            notifyHandler({
              message: "Transaction Successful!",
              color: "green",
            });
            await storage.set(item.memo, await Encrypt(item, wallet.pack.key));
            await router.replace({ name: "Account" });
          } else {
            throw new Error("Transaction Failed!");
          }
        } catch {
          notifyHandler({
            message: "Transaction Failed!",
            color: "negative",
          });
        }
        isPaying.value = false;
      };
      await confirmPin(passFunc);
    };

    const toAccount = async () => {
      router.replace({ name: "Account" });
    };

    return {
      toAccount,
      type,
      asset_code,
      amount,
      memo,
      remarks,
      name,
      zid,
      trans,
      confirmPayment,
      isValid,
      isPaying,
      K,
      iconSet,
      loaded,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1.25rem;
}
.forcewrap {
  word-break: break-all;
}
</style>