<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Audit 核數</h4>
    <div v-if="!isLoaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-else>
      <div v-if="!hasNetwork">
        <p>No network, please retry</p>
        <p>冇網絡 請再試</p>
      </div>
      <div v-else class="infoText">
        <p>{{ K.asset_alias }} {{ K.hkname }}</p>
        <div class="q-pa-none" style="max-width: 350px">
          <q-list bordered padding>
            <q-item-label header style="font-size: 1.25rem"
              >Open data 公開資訊</q-item-label
            >
            <q-item>
              <q-item-section>
                <q-item-label>In circulation 流通量</q-item-label>
                <q-item-label style="font-size: 1rem" caption>
                  {{ K.asset_alias }}
                  {{
                    Number(assetInfo.circulation).toLocaleString(undefined, {
                      minimumFractionDigits: 0,
                    })
                  }}
                </q-item-label>
              </q-item-section>
            </q-item>
            <q-item>
              <q-item-section>
                <q-item-label>Last Updated 更新日期</q-item-label>
                <q-item-label style="font-size: 1rem" caption>
                  {{ hkDateText(assetInfo.date).slice(0, 10) }}
                </q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </div>
      </div>
    </div>
    <div>
      <AppButton text="Back 返去" :action="toAbout" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { readData } from "../composables/readData";
import * as K from "../constants/constants";
import { hkDateText } from "../composables/handlers";

export default {
  components: { AppButton },
  setup() {
    const router = useRouter();
    const isLoaded = ref(false);
    const hasNetwork = ref(true);
    const assetInfo = ref({});

    const toAbout = () => {
      router.replace({ name: "About" });
    };
    const loadData = async () => {
      if (window.navigator.onLine) {
        let cir = await readData();
        K.print(cir);
        assetInfo.value = cir;
      } else {
        hasNetwork.value = false;
      }
      isLoaded.value = true;
    };
    loadData();

    return { K, toAbout, isLoaded, hasNetwork, assetInfo, hkDateText };
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1rem;
}
</style>