<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">NFTs</h4>
    <div v-if="!isLoaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-else>
      <div v-if="assets.length > 0">
        <q-pull-to-refresh @refresh="refresh">
          <q-list>
            <q-item
              v-for="a in assets"
              :key="HtmlSanitizer.SanitizeHtml(a.asset_code)"
            >
              <div class="q-px-none q-pb-sm row items-start q-gutter-md">
                <q-card class="my-card">
                  <q-img
                    :src="HtmlSanitizer.SanitizeHtml(a.thumbnail)"
                    @click="openCard(a)"
                  >
                    <div class="absolute-top text-subtitle2 text-right">
                      NFTs
                    </div>
                  </q-img>

                  <q-card-section>
                    <div
                      class="text-h6"
                      :class="{
                        forcewrap: wrapTitle(
                          HtmlSanitizer.SanitizeHtml(a.name)
                        ),
                      }"
                    >
                      {{ HtmlSanitizer.SanitizeHtml(a.name) }}
                    </div>
                    <div class="text-subtitle2">
                      LOT {{ HtmlSanitizer.SanitizeHtml(a.lot) }} of
                      {{ HtmlSanitizer.SanitizeHtml(a.limit) }}
                    </div>
                  </q-card-section>

                  <q-card-section class="q-pt-none forcewrap">
                    {{ `ipfs://${HtmlSanitizer.SanitizeHtml(a.cid)}` }}
                  </q-card-section>
                  <AppButton text="Open 開" :action="openCard" :args="a" />
                </q-card>
              </div>
            </q-item>
          </q-list>
        </q-pull-to-refresh>
      </div>
      <div v-else>
        <div v-if="!hasNetwork">
          <p>No network, please retry</p>
        </div>
        <div v-else>
          <!-- <p>Welcome to the NFTs page!</p>
          <p>歡迎嚟到良心代幣！</p>
          <p>All sold out, please try again later!</p>
          <p>全部賣清光，明天請早！</p> -->
          <p>Upgrading to ERC1155 standard, be back soon!!</p>
        </div>
        <AppButton text="Back 返去" :action="toAccount" />
      </div>
    </div>
  </div>
  <q-dialog v-model="card" persistent>
    <q-card class="my-card">
      <q-card-section style="max-height: 100vh" class="q-pt-sm scroll">
        <a
          :href="getCidLinks(selected.image.replace('ipfs://', '')).link1"
          target="_blank"
        >
          <q-img :src="selected.thumbnail">
            <div
              class="absolute-top text-subtitle2 text-center"
              :class="{ forcewrap: wrapTitle(selected.name) }"
            >
              {{ selected.name }}
            </div>
          </q-img>
        </a>

        <div class="text-subtitle2">
          LOT {{ selected.lot }} of {{ selected.limit }}
        </div>

        <div
          class="text-subtitle1"
          :class="{ forcewrap: wrapTitle(selected.description) }"
        >
          {{ selected.description }}
        </div>

        <div class="text-caption text-grey forcewrap">
          {{ `ipfs://${selected.cid}(${selected.asset_code})` }}
        </div>

        <q-separator />
        <div v-if="!checkBidded(selected.asset_code)">
          <q-item-label header>
            <span
              v-if="key != K.asset_alias"
              class="infoText"
              @click="
                () => {
                  if (isValid) {
                    showSlider = !showSlider;
                    slideAmt = Number(slideAmt);
                    if (!showSlider) {
                      maxBid = assetAmount;
                    } else {
                      maxBid = 100;
                      slideAmt = Math.min(100, slideAmt);
                    }
                  }
                }
              "
            >
              Bid
              {{
                Number(slideAmt).toLocaleString(undefined, {
                  minimumFractionDigits: countDecimals(slideAmt),
                })
              }}
              {{ K.asset_alias }}
            </span>
            <span class="infoText" v-if="!showSlider">
              <q-input
                outlined
                v-model="slideAmt"
                type="number"
                :step="1"
                :min="10"
                :max="maxBid"
                :error="!isValid"
              />
            </span>
          </q-item-label>
          <div v-if="showSlider && key != K.asset_alias">
            <q-item dense>
              <q-item-section avatar>
                <q-icon name="fas fa-bomb" />
              </q-item-section>
              <q-item-section>
                <q-slider
                  color="primary"
                  v-model="slideAmt"
                  :step="1"
                  :min="10"
                  :max="maxBid"
                />
              </q-item-section>
            </q-item>
          </div>
        </div>
        <div v-else class="text-subtitle1">
          <q-item-label header>
            <span class="infoText"
              >Bidded for 
              {{
                Number(
                  myBids.filter((e) => {
                    return e.memo == selected.asset_code;
                  })[0].amount
                ).toLocaleString(undefined, { minimumFractionDigits: 0 })
              }}
              {{ K.asset_alias }}</span
            >
          </q-item-label>
        </div>

        <div
          v-if="!isPaying"
          class="row q-mb-md q-pb-md justify-between q-px-lg"
        >
          <AppButton
            text="Bid"
            class="q-mb-sm"
            :action="bidHandler"
            :args="selected.asset_code"
            :disabled="
              checkBidded(selected.asset_code) ||
              needRestore ||
              !isValid ||
              assetAmount == 0
            "
          />

          <q-btn
            @click="
              () => {
                showSlider = true;
                maxBid = 100;
                slideAmt = Math.min(maxBid, slideAmt);
                slideAmt = Math.max(10, slideAmt);
                slideAmt = Math.trunc(slideAmt);
              }
            "
            icon="close"
            flat
            rounded
            dense
            v-close-popup
          />
        </div>
        <div v-else style="text-align: center" class="q-pa-md q-mb-md">
          <q-spinner-cube color="primary" size="2.5rem" />
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { ref, computed } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { ReadClaimable } from "../composables/readClaimable";
import {
  nftissuer_zid,
  Operations,
  actions,
  issue_zid,
  nftmanager_zid,
} from "../composables/sdkOperations";
import {
  getCidLinks,
  flashThumbnail,
  getPWADisplayMode,
  readWallet,
  storage,
  notifyHandler,
  confirmPin,
  countDecimals,
} from "../composables/handlers";
var axios = require("axios");
import { Encrypt, Decrypt, sdkEncoder } from "../composables/cipherHandler";
import * as K from "../constants/constants";
import { getClaimants, modes } from "../composables/getClaimants";
import { readBalance } from "../composables/readBalance";
import { dataLink } from "../secrets/secrets";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const assets = ref([]);
    const numberOfEntries = ref(0);
    const router = useRouter();
    const isLoaded = ref(false);
    const needRestore = ref(true);
    const hasNetwork = ref(true);
    const isPaying = ref(false);
    const card = ref(false);
    const selected = ref({});
    const myBids = ref([]);
    const slideAmt = ref(10);
    const showSlider = ref(true);
    const maxBid = ref(100);
    const assetAmount = ref(0);
    const key = ref(K.asset_alias);

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const loadDataNft = async () => {
      numberOfEntries.value = window.history.length;
      if (getPWADisplayMode() == "standalone" && numberOfEntries.value >= 2) {
        router.go(numberOfEntries.value - 1);
      }

      if (K.nftOpen) {
        const wallet = await readWallet();

        try {
          key.value = wallet.pack.key;
        } catch {
          key.value = K.asset_alias;
        }

        if (wallet.secure) {
          if (wallet.secure.mnemonic) {
            needRestore.value = false;
          }
        }

        const nftContent = Decrypt(await storage.get("nftContent"), key.value);
        let nftContentJson;
        try {
          nftContentJson = await JSON.parse(nftContent);
        } catch {
          nftContentJson = [];
        }

        myBids.value = [];
        if (key.value != K.asset_alias) {
          try {
            myBids.value = await JSON.parse(
              Decrypt(await storage.get("myBids"), key.value)
            );
          } catch {
            myBids.value = [];
          }
        }

        if (nftContentJson.length > 0) {
          assets.value = nftContentJson;
          // isLoaded.value = true;
        }

        if (window.navigator.onLine) {
          // isLoaded.value = false;
          if (key.value != K.asset_alias) {
            try {
              assetAmount.value = await readBalance(wallet.disclose.zid, [
                { asset_code: K.asset, asset_issuer: issue_zid },
              ]);
              assetAmount.value = Number(assetAmount.value[0].amount);
            } catch {
              assetAmount.value = 0;
            }
          }

          let claimable = await ReadClaimable(
            nftissuer_zid,
            "",
            true,
            20,
            false
          );

          if (key.value != K.asset_alias) {
            let getMyBids = await ReadClaimable("");
            myBids.value = getMyBids.filter((e) => {
              if (
                e.claimants.length == 2 &&
                sdkEncoder.xidTozid(e.claimants[1].destination) ==
                  wallet.disclose.zid &&
                sdkEncoder.xidTozid(e.sponsor) == wallet.disclose.zid &&
                e.asset.split(":")[0] == K.asset &&
                sdkEncoder.xidTozid(e.asset.split(":")[1]) == issue_zid
              ) {
                try {
                  const claimAfter = Date.parse(
                    new Date(e.claimants[1].predicate.not.abs_before)
                  );
                  K.print(
                    `${(claimAfter - Date.now()) / 1000 / 60} mins to claim`
                  );
                  if (Date.now() < claimAfter) {
                    return true;
                  } else {
                    return false;
                  }
                } catch {
                  return false;
                }
              }
            });
            await storage.set("myBids", await Encrypt(myBids.value, key.value));
          }

          claimable = claimable.filter((e) => {
            let [asset_code, asset_issuer] = e.asset.split(":");
            asset_issuer = sdkEncoder.xidTozid(asset_issuer);
            if (
              asset_issuer == nftissuer_zid &&
              sdkEncoder.xidTozid(e.sponsor) == nftissuer_zid
            ) {
              try {
                getCidLinks(e.cid);
                return true;
              } catch {
                return false;
              }
            }
          });

          const nftIndex = Decrypt(await storage.get("nftIndex"), key.value);

          let indexedNfts;
          try {
            indexedNfts = (await JSON.parse(nftIndex)).map((e) => {
              return e.asset.split(":")[0];
            });
          } catch {
            indexedNfts = [];
          }

          let contentNfts;
          try {
            contentNfts = nftContentJson.map((e) => {
              return e.asset_code;
            });
          } catch {
            contentNfts = [];
          }
          let refreshData = true;
          if (
            nftIndex == JSON.stringify(claimable) &&
            JSON.stringify(indexedNfts) == JSON.stringify(contentNfts)
          ) {
            try {
              assets.value = await JSON.parse(nftContent);
              refreshData = false;
            } catch {
              assets.value = [];
              refreshData = true;
            }
          }
          if (refreshData) {
            isLoaded.value = false;
            await storage.set("nftIndex", await Encrypt(claimable, key.value));

            const metas = await Promise.all(
              claimable.map(async (e) => {
                let [asset_code, asset_issuer] = e.asset.split(":");
                asset_issuer = sdkEncoder.xidTozid(asset_issuer);

                try {
                  const metaLink = getCidLinks(e.cid);
                  const url = metaLink.link1;
                  const preUrl = `${dataLink}nfts/${
                    metaLink.link0.split("/ipfs/")[1]
                  }.json`;
                  let response;
                  try {
                    response = await axios.get(preUrl);
                  } catch {
                    response = await axios.get(url);
                  }
                  let responseJson = response.data;
                  responseJson["cid"] = e.cid;
                  responseJson["asset_code"] = asset_code;
                  responseJson["asset_issuer"] = asset_issuer;
                  responseJson["created_at"] = e.last_modified_time;
                  responseJson["thumbnail"] = await flashThumbnail(
                    responseJson["thumbnail"]
                  );
                  responseJson["bid"] = e.id;
                  return responseJson;
                } catch (err) {
                  K.error(err.message);
                }
              })
            );
            assets.value = metas;
            await storage.set(
              "nftContent",
              await Encrypt(assets.value, key.value)
            );
          }
        } else {
          assets.value = [];
          hasNetwork.value = false;
        }
        assets.value.sort((a, b) => {
          if (a.name < b.name) {
            return -1;
          }
          if (a.name > b.name) {
            return 1;
          }
          if (a.lot < b.lot) {
            return -1;
          }
          if (a.lot > b.lot) {
            return 1;
          }
        });
        await storage.set("nftContent", await Encrypt(assets.value, key.value));
        isLoaded.value = true;
      } else {
        await storage.remove("nftContent");
        await storage.remove("nftIndex");
        isLoaded.value = true;
      }
    };

    const bidHandler = async (code) => {
      const wallet = await readWallet();
      const passFunc = async () => {
        isPaying.value = true;
        const targetxid = sdkEncoder.zidToxid(nftmanager_zid);
        const sourcexid = sdkEncoder.zidToxid(wallet.disclose.zid);
        const claimants = getClaimants(sourcexid, targetxid, modes.submit);
        const trans = [
          {
            action: actions.claimable,
            xid: nftmanager_zid,
            asset_code: K.asset,
            amount: slideAmt.value.toString(),
            claimants,
          },
        ];
        const res = await Operations(trans, code);
        if (res == true) {
          await loadDataNft();
          notifyHandler({
            message: "Bid Submitted!",
            color: "green",
          });
        } else {
          notifyHandler({
            message: "Bid Failed!",
            color: "negative",
          });
        }
        isPaying.value = false;
      };
      await confirmPin(passFunc);
    };

    const openCard = (item) => {
      selected.value = item;
      card.value = true;
    };

    loadDataNft();

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    const checkBidded = (asset_code) => {
      if (key.value != K.asset_alias) {
        let bidder = myBids.value.filter((e) => {
          return e.memo == asset_code;
        });
        if (bidder.length > 0) {
          return true;
        } else {
          return false;
        }
      } else {
        return true;
      }
    };

    return {
      assets,
      toAccount,
      getCidLinks,
      async refresh(done) {
        setTimeout(() => {
          loadDataNft();
          done();
        }, 2500);
      },
      isLoaded,
      hasNetwork,
      needRestore,
      bidHandler,
      isPaying,
      wrapTitle,
      card,
      openCard,
      selected,
      myBids,
      checkBidded,
      slideAmt,
      K,
      HtmlSanitizer,
      isValid: computed(
        () =>
          slideAmt.value >= 10 &&
          slideAmt.value <= maxBid.value &&
          assetAmount.value > 10 &&
          countDecimals(slideAmt.value) == 0
      ),
      showSlider,
      maxBid,
      assetAmount,
      countDecimals,
      key,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

<style scoped>
.forcewrap {
  word-break: break-all;
}
.my-card {
  width: 100%;
  width: calc(90vw);
  max-width: 700px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
.infoText {
  font-size: 1rem;
  font-weight: bold;
  color: black;
}
</style>