<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Shop</h4>
    <p>Welcome to the shop page!</p>
    <div>
      <AppButton text="Back 返去" :action="toAccount" />
    </div>
  </div>
  <q-pull-to-refresh @refresh="refresh"> </q-pull-to-refresh>
</template>

<script>
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const toAccount = () => {
      router.replace({ name: "Account" });
    };
    return {
      toAccount,
      async refresh(done) {
        setTimeout(() => {
          // do refresh
          done();
        }, 2500);
      },
    };
  },
};
</script>

