export const hkDateText = (aDate = "") => {
    if (aDate == "") {
        aDate = new Date(Date.now());
    } else {
        aDate = new Date(aDate);
    };
    var timePart = new Intl.DateTimeFormat('en', { dateStyle: 'full', timeStyle: 'medium' }).format(aDate)
    timePart = timePart.slice(-11, timePart.length);
    let offset = aDate.getTimezoneOffset()
    aDate = new Date(aDate.getTime() - (offset * 60 * 1000))
    const datePart = aDate.toISOString().split('T')[0];
    return `${datePart} ${timePart}`
};


export const updateUser = async(name, url) => {
    const user = projectAuth.currentUser;
    try {
        await user.updateProfile({
            displayName: name,
            photoURL: url,
        });
        K.print("updated");
        return true
    } catch {
        K.error("update failed");
        // alert('Bad connection, please check your internet!')
        notifyHandler({ message: 'Bad connection, please check your internet!', color: 'warning' });
        return false
    }
};

export const clearWallet = async() => {
    await updateUser("", "");
    await storage.remove("secure");

};

export const updateUserHandler = async(data) => {
    let wallet = { disclose: { username: "" }, pack: { pin: "" } };
    try {
        wallet = await readWallet();
    } catch {
        wallet = {};
    }
    if (JSON.stringify(wallet) == "{}") {
        wallet = { disclose: { username: "" }, pack: { pin: "" } };
    }
    if (data.disclose) {
        Object.keys(data.disclose).forEach((e) => {
            wallet.disclose[e] = data.disclose[e];
        });
    }
    if (data.pack) {
        Object.keys(data.pack).forEach((e) => {
            wallet.pack[e] = data.pack[e];
        });
    };
    const updateName = objB64.encode(wallet.disclose);
    const updateUrl = objB64.encode(wallet.pack, true);
    await updateUser(updateName, updateUrl);
};

export const storage = {
    set: async(name, value) => {
        localForage.setDriver([localForage.INDEXEDDB, localForage.LOCALSTORAGE]);
        if (typeof value == "object") {
            value = JSON.stringify(value)
        } else {
            value = value.toString();
        }
        try {
            await localForage.setItem(name, value);
            K.print("saved");
            return true
        } catch (err) {
            K.error(err);
            return false
        }
    },
    get: async(name) => {
        localForage.setDriver([localForage.INDEXEDDB, localForage.LOCALSTORAGE]);
        try {
            const value = await localForage.getItem(name);
            return value
        } catch (err) {
            K.error(err);
            return null
        }
    },
    remove: async(name) => {
        localForage.setDriver([localForage.INDEXEDDB, localForage.LOCALSTORAGE]);
        try {
            await localForage.removeItem(name)
            K.print(`${name} removed`)
        } catch (err) {
            K.error(err.message)
        }
    },
    clear: async() => {
        localForage.setDriver([localForage.INDEXEDDB, localForage.LOCALSTORAGE]);
        try {
            await localForage.clear()
            K.print(`cleared`)
        } catch (err) {
            K.error(err.message)
        }
    }
};

export const getPWADisplayMode = () => {
    const isStandalone = window.matchMedia(
        "(display-mode: standalone)"
    ).matches;
    if (document.referrer.startsWith("android-app://")) {
        return "twa";
    } else if (navigator.standalone || isStandalone) {
        return "standalone";
    }
    return "browser";
};

export const readWallet = async() => {
    let { user } = getUser();
    if (projectAuth.currentUser != null) {
        user = projectAuth.currentUser;
        const name = user.displayName;
        const url = user.photoURL;
        try {
            const disclose = await objB64.decode(name);
            const pack = await objB64.decode(url, true);
            const encrypted = await storage.get("secure");
            let secure = {};
            if (encrypted) {
                try {
                    secure = await JSON.parse(Decrypt(encrypted, pack.key));
                } catch {
                    secure = {}
                }
            }
            const wallet = { disclose, pack, secure }
            K.print(wallet);
            return wallet;
        } catch (err) {
            K.error(err.message);
            return {};
        }
    }
    return {};
};

export const delay = (ms) => new Promise((res) => setTimeout(res, ms));

export const copyToClipboard = (str) => {
    const el = document.createElement("textarea"); // Create a <textarea> element
    el.value = str; // Set its value to the string that you want copied
    el.setAttribute("readonly", ""); // Make it readonly to be tamper-proof
    el.style.position = "absolute";
    el.style.left = "-9999px"; // Move outside the screen to make it invisible
    document.body.appendChild(el); // Append the <textarea> element to the HTML document
    const selected =
        document.getSelection().rangeCount > 0 // Check if there is any content selected previously
        ?
        document.getSelection().getRangeAt(0) // Store selection if found
        :
        false; // Mark as false to know no selection existed before
    el.select(); // Select the <textarea> content
    document.execCommand("copy"); // Copy - only works as a result of a user action (e.g. click events)
    document.body.removeChild(el); // Remove the <textarea> element
    if (selected) {
        // If a selection existed before copying
        document.getSelection().removeAllRanges(); // Unselect everything on the HTML document
        document.getSelection().addRange(selected); // Restore the original selection
    }
};

export const wordFormatter = (badformat) => {
    let better = badformat.toLowerCase().trim().replace(/[\r\n\x0B\x0C\u0085\u2028\u2029]+/g, " ");
    better = better.replace(/\,|\.|\;|\\|\/|\:|\-|\_|\'|\"|\`|\<|\>|\{|\}|\[|\]|\*|\#|\@|\!|\$|\&|\^|\(|\)|\=/g, " ");
    better = better.replace(/[1234567890]+/g, " ");
    let best = "";
    better.split(" ").filter(e => { return e != "" }).forEach(e => { best = best + e + " " });
    best = best.trim();
    return best;
};

export const watchPayment = async(trans = "") => {
    const wallet = await readWallet();
    const key = wallet.pack.key;
    let listen = await storage.get("listen");
    if (trans == "") {
        if (listen) {
            listen = await JSON.parse(Decrypt(listen, key));
            trans = listen;
        }
    }
    if (trans != "") {
        const targetMemo = trans.memo;
        const direction = { asc: "asc", desc: "desc" };

        let limit = 10;
        const xid = sdkEncoder.zidToxid(wallet.disclose.zid);
        let url = "";
        let cursor = wallet.disclose.cursor;

        let lastUpdated = {};
        try {
            lastUpdated = await JSON.parse(Decrypt(await storage.get("lastUpdated"), key));
            if (lastUpdated.cursor > cursor) {
                lastUpdated["cursor"] = cursor;
            };
        } catch {
            lastUpdated["cursor"] = cursor;
            lastUpdated["time"] = Date.now();
        };

        if (lastUpdated) {
            url = `${link}/accounts/${xid}/transactions?cursor=${lastUpdated.cursor}&order=${direction.asc}&limit=${limit}&include_failed=false`;
            cursor = lastUpdated.cursor;
        } else {
            limit = 200;
            url = `${link}/accounts/${xid}/transactions?order=${direction.desc}&limit=${limit}&include_failed=false`;
        };

        let response = await axios.get(url);
        let responseJson = response.data;
        let records = responseJson._embedded.records;
        let setLastUpdate;
        if (records.length > 0 && !lastUpdated) {
            cursor = records[0].paging_token;
            setLastUpdate = { time: Date.now(), cursor };
            K.print(setLastUpdate);
            const enLastUpdate = await Encrypt(setLastUpdate, key);
            await storage.set("lastUpdated", enLastUpdate);
        } else if (records.length > 0 && lastUpdated) {
            cursor = records[records.length - 1].paging_token;
            setLastUpdate = { time: Date.now(), cursor };
            K.print(setLastUpdate);
            const enLastUpdate = await Encrypt(setLastUpdate, key);
            await storage.set("lastUpdated", enLastUpdate);
        }

        let found;
        try {
            found = records.filter((e) => {
                if (e.memo_type == "text") {
                    if (e.memo == targetMemo || distributor_zid == sdkEncoder.xidTozid(e.source_account)) {
                        return true;
                    }
                }
            });
        } catch {
            found = null;
        }

        if (found.length > 0) {
            const endListen = await readOps(found, trans, cursor);
            if (endListen) {
                await storage.remove("listen");
            }
            return found;
        } else {
            let ended = false;
            while (!ended) {
                listen = await storage.get("listen");
                try {
                    lastUpdated = await JSON.parse(Decrypt(await storage.get("lastUpdated"), key));
                } catch {
                    lastUpdated = null;
                }
                if (!listen) {
                    ended = true;
                } else {
                    let proceed = false;
                    if (lastUpdated) {
                        if (Date.now() - K.interval > lastUpdated.time) {
                            proceed = true;
                        }
                    } else {
                        proceed = true;
                    }
                    if (proceed) {
                        url = null;
                        response = null;
                        responseJson = null;
                        records = null;
                        url = `${link}/accounts/${xid}/transactions?cursor=${cursor}&order=${direction.asc}&limit=${limit}&include_failed=false`;
                        response = await axios.get(url);
                        setLastUpdate = { time: Date.now(), cursor };
                        K.print(setLastUpdate);
                        const enLastUpdate = await Encrypt(setLastUpdate, key);
                        await storage.set("lastUpdated", enLastUpdate);
                        responseJson = response.data;
                        records = responseJson._embedded.records;

                        if (records.length > 0) {
                            try {
                                found = records.filter((e) => {
                                    if (e.memo_type == "text") {
                                        if (e.memo == targetMemo || distributor_zid == sdkEncoder.xidTozid(e.source_account)) {
                                            return true;
                                        }
                                    }
                                });
                            } catch {
                                found = null;
                            }
                            if (found.length > 0) {
                                const endListen = await readOps(found, trans, cursor);
                                if (endListen) {
                                    await storage.remove("listen");
                                }
                                ended = true;
                                return found;
                            }
                            cursor = records[records.length - 1].paging_token;
                        }
                    } else {
                        ended = true;
                    }
                    await delay(K.interval);
                }
            }
            return null;
        }
    }
};

export const readOps = async(found, trans, cursor) => {
    try {
        found.forEach(async(afound) => {
            const id = afound.id;
            let url = `${link}/transactions/${id}/operations`;
            let response = await axios.get(url);
            let responseJson = response.data;
            let records = responseJson._embedded.records;
            records.forEach(async(data) => {

                let pass = false;
                let type = data.type;
                let transaction_successful = data.transaction_successful;
                let from = sdkEncoder.xidTozid(data.source_account);
                let to;
                let amount;
                let asset_code;
                let asset_issuer;
                let remarks;
                let memo;

                if (transaction_successful) {
                    if (type == trans.type && afound.memo == trans.memo) {
                        to = sdkEncoder.xidTozid(data.to);
                        amount = data.amount;
                        asset_code = data.asset_code;
                        asset_issuer = sdkEncoder.xidTozid(data.asset_issuer);

                        if (trans.asset_code == asset_code &&
                            trans.zid == to &&
                            asset_issuer == assetDefinition[K.asset].asset_issuer &&
                            parseFloat(amount) - parseFloat(trans.amount) == 0) {
                            remarks = trans.remarks;
                            memo = trans.memo;
                            await storage.remove("listen");
                            // alert(
                            //     `Transaction received!\n\nAmount: ${Number(amount).toLocaleString(undefined, {
                            //         minimumFractionDigits: asset_code == K.asset ? assetDefinition[asset_code].precision : 5,
                            //     })} ${asset_code == K.asset ? K.asset_alias : asset_code}\nRemarks: ${remarks}\nReference: ${memo}\nDate: ${hkDateText(data.created_at)}`
                            // );
                            var shtml = HtmlSanitizer.SanitizeHtml(`Transaction complete!<br><br>Received: ${Number(amount).toLocaleString(undefined, {
                                minimumFractionDigits: asset_code == K.asset ? assetDefinition[asset_code].precision : 5,
                            })} ${asset_code == K.asset ? K.asset_alias : asset_code}<br>Remarks: ${remarks}<br>Reference: ${memo}<br>Date: ${hkDateText(data.created_at)}`);
                            notifyHandler({
                                message: shtml,
                                color: 'green',
                                html: true,
                            });
                            pass = true;
                        }
                    } else if (from == distributor_zid) {
                        if (type == "payment") {
                            to = sdkEncoder.xidTozid(data.to);
                            remarks = "payment";
                        } else if (type == "create_account") {
                            to = sdkEncoder.xidTozid(data.account);
                            remarks = "create_account";
                        } else if (type == "create_claimable_balance") {
                            to = sdkEncoder.xidTozid(data.claimants[0].destination);
                            remarks = "create_claimable_balance";
                        };
                        memo = afound.memo;
                        amount = data.amount;

                        if (data.asset_type == 'native') {
                            asset_issuer = 'native'
                            asset_code = K.gas;
                        } else {
                            asset_issuer = sdkEncoder.xidTozid(data.asset_issuer) || data.asset_type;
                            asset_code = data.asset_code;
                        }
                        // alert(
                        //     `Thank you for you support!\n\nAmount: ${Number(amount).toLocaleString(undefined, {
                        //         minimumFractionDigits: asset_code == K.asset ? assetDefinition[asset_code].precision : 0,
                        //     })} ${asset_code == K.asset ? K.asset_alias : asset_code}\nDate: ${hkDateText(data.created_at)}`
                        // );
                        var shtml = HtmlSanitizer.SanitizeHtml(`Thank you for you support!<br><br>Received: ${Number(amount).toLocaleString(undefined, {
                            minimumFractionDigits: asset_code == K.asset ? assetDefinition[asset_code].precision : 0,
                        })} ${asset_code == K.asset ? K.asset_alias : asset_code}<br>Date: ${hkDateText(data.created_at)}`)
                        notifyHandler({
                            message: shtml,
                            color: 'green',
                            html: true,
                        });
                        pass = true;
                    }

                    if (pass) {
                        const wallet = await readWallet();
                        if (cursor > wallet.disclose.cursor) {
                            await updateUserHandler({ disclose: { cursor: cursor } });
                        };
                        const setLastUpdate = { time: Date.now(), cursor };
                        const key = wallet.pack.key;
                        const enLastUpdate = await Encrypt(setLastUpdate, key);
                        await storage.set("lastUpdated", enLastUpdate);
                        // const exchange = {
                        //     remarks,
                        //     amount,
                        //     asset_code,
                        //     asset_issuer,
                        //     memo,
                        //     from,
                        //     to,
                        //     created_at: data.created_at,
                        //     cursor: data.id,
                        //     id: data.transaction_hash,
                        // };
                        // const encrypted = await Encrypt(exchange, key);
                        // await storage.set(memo, encrypted);
                        return true;
                    }
                }
            })

        })
    } catch (err) {
        K.error(`Invalid Ops: ${err.message}`)
    }
};

export const locateData = (cid, mode = "nft") => {
    return `${dataLink}${mode}/${cid}.json`;
};

export const fetchCid = async(cid, mode = "nft") => {
    const url = locateData(cid, mode);
    let response = await axios.get(
        url
    );
    let responseJson = response.data;
    return responseJson;
};

export const countDecimals = (value) => {
    if ((value % 1) != 0)
        return value.toString().split(".")[1].length;
    return 0;
};

const ipfsLinks = {
    v0: 'https://ipfs.io/ipfs/',
    v1: '.ipfs.dweb.link'
};

export const cidV0toV1 = (cid0) => {
    return new CID(cid0).toV1().toString('base32');
};

export const cidV1toV0 = (cid1) => {
    return new CID(cid1).toV0().toString();
};

export const getCidLinks = (cid0) => {
    const cid1 = cidV0toV1(cid0);
    return { link0: `${ipfsLinks.v0}${cid0}`, link1: `https://${cid1}${ipfsLinks.v1}` };
};

export const flashThumbnail = async(thumbnail) => {
    let url = thumbnail;
    let preUrl = `${dataLink}thumbnails/${cidV1toV0(
        thumbnail.split("https://")[1].split(".ipfs.dweb.link")[0]
    )}.png`;
    try {
        await axios.get(preUrl);
        url = preUrl;
    } catch {
        await axios.get(thumbnail);
        url = thumbnail;
    }
    return url;
};

