import Router from '../router';
export const confirmPin = async(passFunc = async() => {}, failFunc = async() => {}) => {
    const wallet = await readWallet();
    const pin = wallet.pack.pin;
    dialogHandler({
        title: "Pin confirmation",
        message: `Input your security pin`,
        prompt: {
            model: "",
            type: "text",
        },
        persistent: true,
        ok: {
            label: "OK",
            style: { "text-transform": "none" },
            color: "green",
        },
        cancel: {
            label: "Cancel",
            style: { "text-transform": "none" },
        },
    }).onOk(async(inputPin) => {
        const key = wallet.pack.key;
        if (pin == inputPin) {
            let attempts = 0;
            let secure = wallet.secure;
            secure["locked"] = false;
            secure["attempts"] = attempts;
            await storage.set("secure", await Encrypt(secure, key));
            await passFunc();
        } else {
            let attempts = Number(wallet.secure.attempts);
            notifyHandler({
                message: `Pin is invalid. ${K.maxTry - attempts - 1
                    } attempts remaining!`,
                color: "warning",
                html: true,
            });
            if (attempts >= 2) {
                await storage.remove("secure");
                Router.replace('/account')
                await failFunc();
            } else {
                attempts = Number(attempts) + 1;
                let secure = wallet.secure;
                secure["attempts"] = attempts;
                await storage.set("secure", await Encrypt(secure, key));
            }
        }
    });
}

import { ReadClaimable } from "../composables/readClaimable";
import {
    actions,
    Operations,
    trustLimit,
    issue_zid,
    nftissuer_zid,
    auctioneer_zid,
} from "../composables/sdkOperations";
import getAccount from "../composables/readAccount";
export const assetClaimHandler = async(trans) => {
    const { accounts, fetchAccount, records } = getAccount();
    const wallet = await readWallet();
    const claimable = await ReadClaimable("", "", false);
    let claims = claimable.filter((e) => {
        if (
            e.claimants.length == 2 &&
            sdkEncoder.xidTozid(e.claimants[0].destination) ==
            wallet.disclose.zid &&
            sdkEncoder.xidTozid(e.sponsor) != wallet.disclose.zid
        ) {
            return true;
        }
    });

    let bids = claimable.filter((e) => {
        if (
            e.claimants.length == 2 &&
            sdkEncoder.xidTozid(e.claimants[1].destination) ==
            wallet.disclose.zid &&
            sdkEncoder.xidTozid(e.sponsor) == wallet.disclose.zid &&
            e.asset.split(":")[0] == K.asset &&
            sdkEncoder.xidTozid(e.asset.split(":")[1]) == issue_zid
        ) {
            try {
                const claimAfter = Date.parse(
                    new Date(e.claimants[1].predicate.not.abs_before)
                );
                K.print(
                    `${(claimAfter - Date.now()) / 1000 / 60} mins to claim`
                );
                if (Date.now() > claimAfter) {
                    return true;
                } else {
                    return false;
                }
            } catch {
                return false;
            }
        }
    });
    claims = [...claims, ...bids];

    if (claims.length > 0) {
        claims = claims.map((e) => {
            let [aCode, aIssuer] = e.asset.split(":");
            aIssuer = sdkEncoder.xidTozid(aIssuer);
            if (aCode != K.asset || aIssuer != issue_zid) {
                if (aIssuer == nftissuer_zid || aIssuer == auctioneer_zid) {
                    trans.push({
                        action: actions.trust,
                        xid: aIssuer,
                        asset_code: aCode,
                        amount: trustLimit.MIN,
                    });
                }
            }
            return { action: actions.claim, xid: e.id };
        });
        trans = [...trans, ...claims];
    }
    accounts.value.forEach((e) => {
        if (Number(e.amount) == 0 && e.nft) {
            const needToKeep = claimable.filter((c) => {
                return (
                    c.asset ==
                    `${e.asset_code}:${sdkEncoder.zidToxid(e.asset_issuer)}`
                );
            });
            if (needToKeep.length == 0) {
                trans.push({
                    action: actions.trust,
                    xid: e.asset_issuer,
                    asset_code: e.asset_code,
                    amount: trustLimit.NIL,
                });
            }
        }
    });
    K.print(trans);
    if (trans.length > 0) {
        await Operations(trans, "auto claim");
    }
    return trans;
};

