<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Transactions 對數</h4>
    <div class="q-ma-none q-pa-none" v-if="!loaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-else class="q-ma-none q-pa-none">
      <div v-if="exchanges.length > 0" class="q-ma-none q-pa-none">
        <q-list>
          <q-item class="my-item" v-for="a in exchanges" :key="a.asset_code">
            <q-item-section>
              <q-item-label>
                <span class="text-bold infoText">
                  {{ a.source_account == wallet.disclose.zid ? "-" : "+" }}
                  <span v-if="a.amount">
                    <q-icon :name="iconSet[a.asset_code]" />
                  </span>
                  {{ a.asset_code == K.asset ? K.asset_alias : a.asset_code }}
                  {{
                    a.amount
                      ? Number(a.amount).toLocaleString(undefined, {
                          minimumFractionDigits: 2,
                        })
                      : ""
                  }}
                </span>
              </q-item-label>
              <q-item-label
                v-if="a.name && a.source_account == wallet.disclose.zid"
                class="infoText"
              >
                {{ a.source_account == wallet.disclose.zid ? "To" : "From" }}:
                {{ HtmlSanitizer.SanitizeHtml(a.name) }}
              </q-item-label>
              <q-item-label v-if="a.remarks" class="infoText">
                Remarks: {{ HtmlSanitizer.SanitizeHtml(a.remarks) }}
              </q-item-label>
              <q-item-label class="infoText">
                {{ hkDateText(a.created_at) }}
              </q-item-label>
              <q-item-label
                v-if="a.source_account != wallet.disclose.zid"
                class="infoText forcewrap"
              >
                From:
                {{
                  a.from == issue_zid ||
                  a.from == distributor_zid ||
                  a.from == auctioneer_zid ||
                  a.from == bookkeeper_zid ||
                  a.from == nftmanager_zid ||
                  a.from == nftissuer_zid
                    ? "FreeHK.io"
                    : HtmlSanitizer.SanitizeHtml(a.from)
                }}
              </q-item-label>
              <q-item-label
                v-if="a.source_account == wallet.disclose.zid"
                class="infoText forcewrap"
              >
                To:
                {{
                  a.to == issue_zid ||
                  a.to == distributor_zid ||
                  a.to == auctioneer_zid ||
                  a.to == bookkeeper_zid ||
                  a.to == nftmanager_zid ||
                  a.to == nftissuer_zid
                    ? "FreeHK.io"
                    : HtmlSanitizer.SanitizeHtml(a.to)
                }}
              </q-item-label>
              <q-item-label class="infoText forcewrap">
                Ref: {{ a.memo }}
              </q-item-label>
            </q-item-section>
          </q-item>
        </q-list>
      </div>
      <div v-else class="q-ma-none q-pa-none">
        <p class="infoText">Welcome to the Transactions page!</p>
        <p class="infoText">歡迎嚟到對數!</p>
        <div>
          <AppButton text="Back 返回" :action="toAccount" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { readWallet, hkDateText, storage } from "../composables/handlers";
import {
  sdkEncoder,
  Decrypt,
  Encrypt,
} from "../composables/cipherHandler";
import { link } from "../secrets/sdkHandler";
var EventSource = require("eventsource");
import * as K from "../constants/constants";
import { iconSet } from "../constants/iconSet";
import {
  issue_zid,
  distributor_zid,
  auctioneer_zid,
  bookkeeper_zid,
  nftmanager_zid,
  nftissuer_zid,
} from "../composables/sdkOperations";
const axios = require("axios");

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const loaded = ref(false);
    const exchanges = ref([]);
    const wallet = ref({});
    const es = ref({});
    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const loadData = async () => {
      let p_type = "payments";
      wallet.value = await readWallet();
      let cursor = 0;
      try {
        let history = await JSON.parse(
          Decrypt(await storage.get("history"), wallet.value.pack.key)
        );
        if (!history) {
          throw new Error("no history");
        } else {
          cursor = history.cursor;
          exchanges.value = history.records;
        }
      } catch (err) {
        K.error(err.message);
        cursor = 0;
      }

      const xid = sdkEncoder.zidToxid(wallet.value.disclose.zid);

      const p_url = `${link}/accounts/${xid}/${p_type}?cursor=${cursor}`;

      es.value = new EventSource(p_url);

      es.value.onmessage = async function (message) {
        let result = message.data ? JSON.parse(message.data) : message;
        const t_url = `${link}/transactions/${result.transaction_hash}`;
        let memo = "";
        result["memo"] = result.paging_token;
        try {
          let response = await axios.get(t_url);
          if (response.data) {
            let responseJson = await response.data;
            memo = responseJson.memo;
            result["memo"] = memo;
          }
        } catch (err) {
          K.error(err.message);
        }
        cursor = result.paging_token;
        await storage.set(
          "history",
          await Encrypt(
            { cursor, records: exchanges.value },
            wallet.value.pack.key
          )
        );

        if (result.type == "create_account" || result.type == "payment") {
          if (
            result.asset_type == "native" ||
            result.type == "create_account"
          ) {
            result["asset_code"] = K.gas;
            result["asset_issuer"] = "native";
            if (result.type == "create_account") {
              result["amount"] = result.starting_balance;
              result["from"] = result.source_account;
              result["to"] = result.account;
              result["funder"] = sdkEncoder.xidTozid(result["funder"]);
              result["account"] = sdkEncoder.xidTozid(result["account"]);
            }
          } else {
            result["asset_issuer"] = sdkEncoder.xidTozid(
              result["asset_issuer"]
            );

            try {
              const key = wallet.value.pack.key;
              const catchVal = await JSON.parse(
                Decrypt(await storage.get(memo), key)
              );
              result["remarks"] = catchVal.remarks;
              if (catchVal.name) {
                result["name"] = catchVal.name;
              }
              await storage.remove(memo);
            } catch (err) {
              K.print("no such remarks");
            }
          }
          result["from"] = sdkEncoder.xidTozid(result["from"]);
          result["to"] = sdkEncoder.xidTozid(result["to"]);
          result["source_account"] = sdkEncoder.xidTozid(
            result["source_account"]
          );

          if (
            result["from"] == issue_zid ||
            result["from"] == distributor_zid ||
            result["from"] == auctioneer_zid ||
            result["from"] == bookkeeper_zid ||
            result["from"] == nftmanager_zid ||
            result["from"] == nftissuer_zid
          ) {
            result["from"] = "FreeHK.io";
          }

          if (
            result["to"] == issue_zid ||
            result["to"] == distributor_zid ||
            result["to"] == auctioneer_zid ||
            result["to"] == bookkeeper_zid ||
            result["to"] == nftmanager_zid ||
            result["to"] == nftissuer_zid
          ) {
            result["to"] = "FreeHK.io";
          }

          if (
            result["source_account"] == issue_zid ||
            result["source_account"] == distributor_zid ||
            result["source_account"] == auctioneer_zid ||
            result["source_account"] == bookkeeper_zid ||
            result["source_account"] == nftmanager_zid ||
            result["source_account"] == nftissuer_zid
          ) {
            result["source_account"] = "FreeHK.io";
          }

          if (
            (result.asset_code == K.asset &&
              result.asset_issuer == issue_zid) ||
            result.asset_code == K.gas
          ) {
            let isExist = exchanges.value.filter((e) => {
              return e.paging_token == result.paging_token;
            });
            if (isExist.length == 0) {
              exchanges.value.unshift(result);
              exchanges.value.sort((a, b) => {
                if (a.created_at > b.created_at) {
                  return -1;
                }
                if (a.created_at < b.created_at) {
                  return 1;
                }
                if (a.asset_code < b.asset_code) {
                  return -1;
                }
                if (a.asset_code < b.asset_code) {
                  return 1;
                }
              });
              let newHistory = {
                cursor,
                records: exchanges.value,
              };
              const encrypted = await Encrypt(
                newHistory,
                wallet.value.pack.key
              );
              await storage.set("history", encrypted);
            };
          };
        };
      };
      loaded.value = true;
    };

    loadData();

    return {
      toAccount,
      exchanges,
      iconSet,
      K,
      hkDateText,
      wallet,
      loaded,
      es,
      issue_zid,
      distributor_zid,
      auctioneer_zid,
      bookkeeper_zid,
      nftmanager_zid,
      nftissuer_zid,
      HtmlSanitizer,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.es) {
      this.es.close();
    }
    next();
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1rem;
}
.forcewrap {
  word-break: break-all;
  font-size: 0.9rem;
  color: grey;
}
</style>