var axios = require('axios');
import * as SH from '../secrets/sdkHandler';
const sdk = SH.sdk;

import * as K from "../constants/constants";
import { sdkEncoder } from "../composables/cipherHandler";
import { readWallet } from "../composables/handlers"
import { assetDefinition } from '../secrets/secrets';

const zid = assetDefinition[K.asset].asset_issuer;
const issue_xid = sdkEncoder.zidToxid(zid);
const baseAsset = `${K.asset}:${issue_xid}`;

const link = SH.link;
let server = SH.newServer();

export const ReadClaimable = async(zid = "", asset = baseAsset, fetchMemo = true, limit = 20, asc = true, cursor = "") => {
    try {
        if (zid == "") {
            const wallet = await readWallet();
            zid = wallet.disclose.zid
        }

        let xid = sdkEncoder.zidToxid(zid);
        let B = sdk.Keypair.fromPublicKey(xid);
        let baseClaimable = server
            .claimableBalances()
            .claimant(B.publicKey())
            .cursor(cursor)
            .limit(limit);

        if (asset == K.gas) {
            baseClaimable.asset('native');
        } else {
            baseClaimable.asset(asset);
        };
        if (asc) {
            baseClaimable.order("asc");
        } else {
            baseClaimable.order("desc");
        };

        const balances = await baseClaimable.call();
        if (!balances) { return balanceIds; }
        let records = balances.records;
        var balanceIds = [...records];
        await Promise.all(records.map(async(balance) => {
            let memo = null;
            let cid = memo;
            if (fetchMemo) {
                const url = `${link}/claimable_balances/${balance.id}/transactions`;
                const response = await axios.get(url);
                let responseJson = response.data;
                var txs = responseJson._embedded.records;
                memo = txs[0].memo;
                balance['memo'] = memo;
                const decode = sdkEncoder.MemoToCid(memo);
                if (decode.length == 46 && decode.slice(0, 2) == "Qm") {
                    cid = decode;
                    balance['cid'] = cid;
                };
            };

        }));
    } catch (err) {
        K.error(err.message);
    };
    return balanceIds;
};