<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Mnemonic</h4>
    <div>
      <div v-if="mnemonic != 'Please restore mnemonic'">
        <p class="infoText">{{ HtmlSanitizer.SanitizeHtml(showMnemonic) }}</p>
        <div class="q-mt-xl">
          <AppButton :text="buttonName" :action="displayMnemonic" />
        </div>
      </div>

      <div v-else>
        <div class="input-group">
          <div class="inputText" style="max-width: 700px">
            <q-input
              placeholder="Input mnemonic"
              v-model="restoreMnemonic"
              autogrow
              style="font-size: 1.25rem"
            >
              <template v-slot:append>
                <q-icon
                  name="close"
                  @click="restoreMnemonic = ''"
                  class="cursor-pointer"
                />
              </template>
            </q-input>
          </div>
        </div>
        <AppButton text="Restore" :action="handleMnemonic" />
      </div>

      <div v-if="mnemonic != 'Please restore mnemonic'">
        <AppButton text="Back" :action="goBack" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import {
  readWallet,
  storage,
  copyToClipboard,
  delay,
  wordFormatter,
  notifyHandler,
  dialogHandler,
} from "../composables/handlers";
import {
  sdkEncoder,
  Encrypt,
  validateMnemonic,
  getAccount,
} from "../composables/cipherHandler";
import { projectAuth } from "../secrets/fbsecret";
import * as K from "../constants/constants";
import { transferPhrase } from "../secrets/secrets";

export default {
  name: "Mnemonic",
  components: { AppButton },
  emits: ["reload"],
  props: ["mnemonic", "backTo"],
  setup(props) {
    const mnemonic = ref(props.mnemonic);
    const restoreMnemonic = ref("");
    const router = useRouter();
    const wallet = ref({});
    const isShow = ref(false);
    const baseMnemonic =
      "****** ****** ****** ****** ****** ****** ****** ****** ****** ****** ****** ******";
    const buttonName = ref("Show Mnemonic for 20 seconds");
    const showMnemonic = ref(baseMnemonic);

    const loadData = async () => {
      if (!props.mnemonic) {
        wallet.value = await readWallet();
        if (wallet.value.secure.mnemonic) {
          mnemonic.value = wallet.value.secure.mnemonic;
        } else {
          mnemonic.value = "Please restore mnemonic";
        }
      }
    };
    loadData();

    const handleMnemonic = async () => {
      wallet.value = await readWallet();
      const zid = wallet.value.disclose.zid;
      const key = wallet.value.pack.key;
      const cxid = sdkEncoder.zidToxid(zid);
      const user = projectAuth.currentUser;
      const uid = user.uid;
      let salt = "";
      if (wallet.value.pack.salt) {
        salt = wallet.value.pack.salt;
      }
      const entropy = uid + salt + transferPhrase;
      if (restoreMnemonic.value) {
        const dismiss = notifyHandler({ message: "restoring...", timeout: 0 });
        let dataMnemonic = HtmlSanitizer.SanitizeHtml(restoreMnemonic.value);
        dataMnemonic = wordFormatter(dataMnemonic);
        if (validateMnemonic(dataMnemonic)) {
          const wallet = getAccount(dataMnemonic, entropy);
          const xid = wallet.xid;
          const seed = wallet.seed;
          const zeed = sdkEncoder.xidTozid(seed);
          K.print(zeed);
          K.print(dataMnemonic);
          if (cxid == xid) {
            K.print(cxid);
            K.print(xid);
            const secure = {
              zeed,
              mnemonic: dataMnemonic,
              zid,
              backedUp: true,
              attempts: 0,
              locked: false,
            };
            const encrypted = await Encrypt(secure, key);
            await storage.set("secure", encrypted);
            var shtml = HtmlSanitizer.SanitizeHtml(
              "Mnemonic restored!"
            );
            notifyHandler({
              message: shtml,
              html: true,
              color: "green",
            });

            await router.replace({ name: "Account" });
          } else {
            var shtml = HtmlSanitizer.SanitizeHtml(
              "Wrong mnemonic, please try again!"
            );
            notifyHandler({
              message: shtml,
              html: true,
              color: "warning",
            });
          }
        } else {
          var shtml = HtmlSanitizer.SanitizeHtml(
            "Wrong mnemonic, please try again!"
          );
          notifyHandler({
            message: shtml,
            html: true,
            color: "warning",
          });
        }
        dismiss();
      } else {
        var shtml = HtmlSanitizer.SanitizeHtml(
          "Wrong mnemonic, please try again!"
        );
        notifyHandler({
          message: shtml,
          html: true,
          color: "warning",
        });
      }
    };

    const goBack = async () => {
      let wallet = await readWallet();
      if (
        wallet.secure.backedUp ||
        mnemonic.value == "Please restore mnemonic"
      ) {
        router.replace({ name: props.backTo });
      } else {
        const place = Math.floor(Math.random() * 12);
        const item = [
          "1st",
          "2nd",
          "3rd",
          "4th",
          "5th",
          "6th",
          "7th",
          "8th",
          "9th",
          "10th",
          "11th",
          "12th",
        ];
        const target = mnemonic.value.split(" ")[place];
        dialogHandler({
          title: "Authentication",
          message: `What is the ${
            item[place]
          } secret phrase of your mnemonic?`,
          html: true,
          prompt: {
            model: "",
            type: "text",
          },
          persistent: true,
          ok: {
            label: "OK",
            style: { "text-transform": "none" },
            color: "green",
          },
          cancel: {
            label: "Cancel",
            style: { "text-transform": "none" },
          },
        }).onOk(async (inputMnemonic) => {
          inputMnemonic = wordFormatter(inputMnemonic);
          if (target == inputMnemonic) {
            let secure = wallet.secure;
            secure["backedUp"] = true;
            const encrypted = await Encrypt(secure, wallet.pack.key);
            await storage.set("secure", encrypted);
            await router.replace({ name: "Account" });
          } else {
            notifyHandler({
              message: "Invalid input!",
              color: "warning",
            });
          }
        });
      }
    };

    const copy = async () => {
      copyToClipboard(mnemonic.value);
      await delay(500);
    };

    const displayMnemonic = async () => {
      showMnemonic.value = mnemonic.value;
      for (let i = 0; i < 20; i++) {
        let count = 20 - i;
        buttonName.value = `Show Mnemonic for ${count} seconds`;
        await delay(1000);
      }
      showMnemonic.value = baseMnemonic;
      buttonName.value = "Show Mnemonic for 20 seconds";
    };

    return {
      goBack,
      mnemonic,
      restoreMnemonic,
      handleMnemonic,
      wallet,
      copy,
      isShow,
      showMnemonic,
      displayMnemonic,
      buttonName,
      HtmlSanitizer,
    };
  },
};
</script>

<style scoped>
.inputText input {
  font-size: 1.25rem;
}
.infoText {
  font-size: 1.25rem;
}
.btnStyle button {
  min-width: 40%;
  font-size: 1rem;
}
</style>