import {
    createRouter,
    createWebHistory,
    // createWebHashHistory,
} from 'vue-router'
import Account from '../views/Account.vue'
import About from '../views/About.vue'
import Posts from '../views/Posts.vue'
import Article from '../views/Article.vue'
import Terms from '../views/Terms.vue'
import Code from '../views/Code.vue'
import Collectables from '../views/Collectables.vue'
import NFTs from '../views/NFTs.vue'
import Settings from '../views/Settings.vue'
import Shop from '../views/Shop.vue'
import Trade from '../views/Trade.vue'
import Mnemonic from '../views/Mnemonic.vue'
import Give from '../views/Give.vue'
import Scan from '../views/Scan.vue'
import Take from '../views/Take.vue'
import Transactions from '../views/Transactions.vue'
import Help from '../views/Help.vue'
import Dashboard from '../views/Dashboard.vue'
import Audit from '../views/Audit.vue'
import Confirm from '../views/Confirm.vue'
import { projectAuth } from '../secrets/fbsecret'
import { objB64, Encrypt } from "../composables/cipherHandler";
import { readWallet, storage } from "../composables/handlers";
import * as K from "../constants/constants";

const requireAuth = async(to, from, next) => {
    let user = projectAuth.currentUser
    if (user) {
        let wallet = await readWallet();
        if (wallet.secure) {
            if (wallet.secure.mnemonic) {
                if (!wallet.secure.locked) {
                    next()
                    return
                }
            }
        }
    }
    next({ name: 'Account' })
}

const requireRestore = async(to, from, next) => {
    let user = projectAuth.currentUser
    if (user) {
        let wallet = await readWallet();
        if (wallet.secure) {
            if (wallet.secure.mnemonic) {
                next()
                return
            }
        }
    }
    next({ name: 'Account' })
}

const requireConfirmation = async(to, from, next) => {
    let wallet = await readWallet();
    try {
        if (to.params.mnemonic || !wallet.secure.mnemonic) {
            next()
        } else {
            let user = projectAuth.currentUser
            if (!user) {
                next({ name: 'Account' })
            } else {
                const url = user.photoURL
                const pack = await objB64.decode(url, true);
                let inputPin = prompt("What's your pin?");
                let attempts = Number(wallet.secure.attempts);
                if (pack.pin == inputPin) {
                    let secure = wallet.secure;
                    secure["attempts"] = 0;
                    await storage.set("secure", await Encrypt(secure, wallet.pack.key));
                    next()
                } else {
                    if (attempts >= K.maxTry - 1) {
                        await storage.remove("secure");
                    } else {
                        attempts = attempts + 1;
                        let secure = wallet.secure;
                        secure["attempts"] = attempts;
                        await storage.set("secure", await Encrypt(secure, wallet.pack.key));
                    };
                    next(from)
                }
            }
        }
    } catch {
        next(from)
    }

}

const routes = [{
        path: '/',
        name: 'Account',
        component: Account
    },
    {
        path: '/about',
        name: 'About',
        component: About
    },
    {
        path: '/posts',
        name: 'Posts',
        component: Posts,
    },
    {
        path: '/terms',
        name: 'Terms',
        component: Terms,
    },
    {
        path: '/code',
        name: 'Code',
        component: Code,
    },
    {
        path: '/article',
        name: 'Article',
        component: Article,
        props: true,
    },
    {
        path: '/collectables',
        name: 'Collectables',
        component: Collectables,
        beforeEnter: requireAuth
    },
    {
        path: '/nfts',
        name: 'NFTs',
        component: NFTs,
    },
    {
        path: '/settings',
        name: 'Settings',
        component: Settings,
        beforeEnter: requireAuth
    },
    {
        path: '/trade',
        name: 'Trade',
        component: Trade,
        beforeEnter: requireAuth
    },
    {
        path: '/shop',
        name: 'Shop',
        component: Shop,
        beforeEnter: requireAuth
    },
    {
        path: '/trade',
        name: 'Trade',
        component: Trade,
        beforeEnter: requireAuth
    },
    {
        path: '/mnemonic',
        name: 'Mnemonic',
        component: Mnemonic,
        beforeEnter: requireConfirmation,
        props: true,
    },
    {
        path: '/give',
        name: 'Give',
        component: Give,
        beforeEnter: requireAuth,
    },
    {
        path: '/scan',
        name: 'Scan',
        component: Scan,
        beforeEnter: requireAuth,
    },
    {
        path: '/take',
        name: 'Take',
        component: Take,
        beforeEnter: requireRestore,
    },
    {
        path: '/confirm',
        name: 'Confirm',
        component: Confirm,
        beforeEnter: requireRestore,
        props: true,
    },
    {
        path: '/transactions',
        name: 'Transactions',
        component: Transactions,
        beforeEnter: requireAuth,
        props: true,
    },
    {
        path: '/help',
        name: 'Help',
        component: Help,
    },
    {
        path: '/dashboard',
        name: 'Dashboard',
        component: Dashboard,
    },
    {
        path: '/audit',
        name: 'Audit',
        component: Audit,
    },
    {
        path: "/:pathMatch(.*)*",
        name: 'Account',
        component: Account
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router