<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold" style="text-align: center">
      Posts 前文
    </h4>
    <div
      class="q-pa-none q-ma-none"
      style="text-align: center"
      v-if="!isLoaded"
    >
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-else class="q-pa-none q-ma-none">
      <q-pull-to-refresh @refresh="refresh">
        <div v-if="posts.length > 0 && hasNetwork" class="q-ma-none q-pa-none">
          <div class="q-pa-none q-ma-none" style="max-width: 700px">
            <q-list>
              <q-item
                class="my-item"
                v-for="a in posts"
                :key="a.id"
                clickable
                @click="openArticle(a)"
              >
                <q-item-section>
                  <p class="infoText">
                    {{ a.title }}
                  </p>
                  <q-item-label caption>
                    <p class="dateText">{{ a.id.slice(0, 10) }}</p>
                  </q-item-label>
                </q-item-section>
              </q-item>
            </q-list>
          </div>
        </div>
        <div v-else>
          <p>Nothing to see 冇嘢睇</p>
          <AppButton text="Back 返去" :action="toAccount" />
        </div>
      </q-pull-to-refresh>
    </div>
  </div>
  <q-pull-to-refresh @refresh="refresh"> </q-pull-to-refresh>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
const axios = require("axios");
import { postsLink } from "../constants/appLinks";
import * as K from "../constants/constants";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const posts = ref([]);
    const isLoaded = ref(false);
    const hasNetwork = ref(true);
    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const openArticle = async (item) => {
      let back = { name: "Posts" }
      await router.replace({
        name: "Article",
        params: { id: item.id, back: JSON.stringify(back) },
      });
    };

    const loadData = async () => {
      if (window.navigator.onLine) {
        try {
          let response = await axios.get(postsLink);
          let responseJson = response.data;
          posts.value = responseJson.index;
        } catch (err) {
          K.error(err.message);
          posts.value = [];
        }
      } else {
        hasNetwork.value = false;
      }
      isLoaded.value = true;
    };

    loadData();

    return {
      toAccount,
      posts,
      isLoaded,
      hasNetwork,
      openArticle,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
    };
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1.25rem;
}
.dateText {
  font-size: 1rem;
}
</style>