<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">
      Take 收 {{ K.asset_alias }}
    </h4>
    <div v-show="!paymentPending">
      <div class="q-pt-sm text-center infoText">
        <q-input
          v-model="userAmount"
          style="font-size: 1.25rem"
          :rules="[
            (val) =>
              handleDecimal(val) ||
              `Input a number with at most ${
                assetDefinition[K.asset].precision
              } decimals`,
          ]"
          :label="K.asset_alias + ' amount ' + K.hkname + '數量:'"
          input-class="text-right"
          :disable="paymentPending"
        />
      </div>
      <div class="q-pt-md text-center infoText">
        <q-input
          v-model="remarks"
          style="font-size: 1.25rem"
          :rules="[(val) => val.length >= 1 || 'Input at least 1 character']"
          label="Remarks 備註:"
          autogrow
          :disable="paymentPending"
          maxlength="80"
          counter
        />
      </div>
    </div>

    <div v-if="qrContent != 'nil'">
      <QRcode :text="qrContent" />
    </div>
  </div>
  <div class="q-my-5">
    <div
      v-show="
        userAmount > 0 &&
        handleDecimal(userAmount) &&
        remarks.length > 0 &&
        paymentPending == false
      "
    >
      <AppButton text="Proceed 去䃙" :action="changeQR" />
    </div>
    <div v-if="paymentPending" style="text-align: center" class="forcewrap">
      <q-list separator style="max-width: 700px">
        <q-item clickable v-ripple>
          <q-item-section>
            <q-item-label>Receive: {{ userAmount }} {{ K.asset }}</q-item-label>
            <q-item-label>Remarks: {{ remarks }}</q-item-label>
            <q-item-label caption>Reference: {{ memo }}</q-item-label>
          </q-item-section>
        </q-item>
      </q-list>

      <q-spinner-cube color="primary" size="2.5rem" />
      <AppButton :text="copied" :action="handleCopy" />
    </div>
    <AppButton :text="btnText()" :action="goBack" />
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import QRcode from "../components/QRcode.vue";
import AppButton from "../components/AppButton.vue";
import {
  readWallet,
  storage,
  copyToClipboard,
  delay,
  watchPayment,
  readOps,
  countDecimals,
  dialogHandler,
  maxBalanceNotify,
} from "../composables/handlers";
import { getPassword, Encrypt, Decrypt } from "../composables/cipherHandler";

import { projectAuth } from "../secrets/fbsecret";
import * as K from "../constants/constants";
import { transferPhrase, assetDefinition } from "../secrets/secrets";
const { Base64 } = require("js-base64");

export default {
  components: { QRcode, AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const userAmount = ref("");
    const qrContent = ref("nil");
    const paymentCode = ref("");
    const remarks = ref("");
    const paymentPending = ref(false);
    const memo = ref(getPassword(16, true, false));
    const copied = ref("Copy Code 抄交易編䃙");
    const assetAmount = ref(0);
    const exceed = ref(false);

    const btnText = () => {
      if (paymentPending.value) {
        return "Cancel 取消";
      } else {
        return "Back 返去";
      }
    };

    const currentRoute = () => {
      return router.currentRoute.value.name;
    };

    const handleCopy = async () => {
      copyToClipboard(paymentCode.value);
      copied.value = "Copied 已抄";
      await delay(500);
      copied.value = "Copy Code 抄交易編䃙";
    };

    const changeQR = async () => {
      const asset_code = K.asset;
      const amount = HtmlSanitizer.SanitizeHtml(userAmount.value);
      const type = "payment";
      let trans = {
        type,
        asset_code,
        amount,
        mode: !K.devMode,
        memo: HtmlSanitizer.SanitizeHtml(memo.value),
        remarks: HtmlSanitizer.SanitizeHtml(remarks.value),
      };
      let key = "";
      if (projectAuth.currentUser != null) {
        try {
          const wallet = await readWallet();
          const accountBalances = Decrypt(
            await storage.get("balances"),
            wallet.pack.key
          );
          let balances;
          if (JSON.stringify(accountBalances) != "") {
            balances = await JSON.parse(accountBalances);
            try {
              assetAmount.value = balances.filter((e) => {
                return e.asset_code == K.asset;
              })[0];
              if (
                Number(assetAmount.value.amount) + Number(amount) >=
                Number(assetAmount.value.limit)
              ) {
                exceed.value = true;
              }
            } catch {
              assetAmount.value = 0;
            }
          }
          const name = wallet.disclose.username;
          const zid = wallet.disclose.zid;
          trans["name"] = name;
          trans["zid"] = zid;
          key = wallet.pack.key;
        } catch (err) {
          K.error(err.message);
        }
      }
      if (exceed.value == true) {
        await maxBalanceNotify(assetAmount.value.limit);
      } else {
        qrContent.value = Base64.encode(JSON.stringify(trans));
        const encrypted = await Encrypt(trans, key);
        const transCode = await Encrypt(trans, transferPhrase);
        paymentCode.value = transCode;

        let listen = await storage.get("listen");
        if (!listen || !paymentPending.value) {
          await storage.set("listen", encrypted);
          await storage.set(memo.value, encrypted);
          paymentPending.value = true;
        }
        let lastUpdated = await storage.get("lastUpdated");
        let proceed = false;
        if (!lastUpdated) {
          proceed = true;
        } else {
          try {
            lastUpdated = await JSON.parse(Decrypt(lastUpdated, key));
            if (Date.now() - K.interval > lastUpdated.time) {
              proceed = true;
            }
          } catch {
            proceed = true;
          }
        }
        if (proceed) {
          await watchPayment(trans);
          await storage.remove("listen");
          if (paymentPending.value) {
            await router.replace({ name: "Account" });
          }
          paymentPending.value = false;
        }
      }
    };

    const goBack = async () => {
      if (paymentPending.value) {
        dialogHandler({
          title: "Transaction in progress 交易緊!",
          message: "Confirm to end transaction?<br>確認終止？",
          persistent: true,
          ok: {
            label: "End 終止",
            style: { "text-transform": "none" },
            color: "red",
          },
          cancel: {
            label: "Continue 繼續",
            style: { "text-transform": "none" },
          },
          html: true,
        }).onOk(async () => {
          await storage.remove("listen");
          await storage.remove(memo.value);
          paymentPending.value = false;
          await router.replace({ name: "Account" });
        });
      } else {
        await router.replace({ name: "Account" });
      }
    };

    const loadReceive = async () => {
      const wallet = await readWallet();
      const key = wallet.pack.key;
      let listen = await storage.get("listen");
      if (listen) {
        try {
          listen = await JSON.parse(Decrypt(listen, key));
          userAmount.value = listen.amount;
          remarks.value = listen.remarks;
          memo.value = listen.memo;
          paymentPending.value = true;
          await changeQR();
        } catch {
          listen = {};
        }
      }
    };

    const handleDecimal = (
      value,
      decimals = assetDefinition[K.asset].precision
    ) => {
      if (Number(value).toString() != "NaN") {
        if (countDecimals(value) <= decimals) {
          return true;
        }
      }
      return false;
    };

    loadReceive();

    return {
      qrContent,
      changeQR,
      goBack,
      userAmount,
      paymentPending,
      remarks,
      memo,
      handleCopy,
      copied,
      readOps,
      K,
      currentRoute,
      btnText,
      paymentCode,
      handleDecimal,
      assetDefinition,
      maxBalanceNotify,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    let listen = await storage.get("listen");
    if (listen) {
      if (confirm("Cancel transaction?")) {
        await storage.remove("listen");
        await storage.remove(this.memo);
        this.paymentPending = false;
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },
};
</script>

<style scoped>
.infoText input {
  font-size: 1.25rem;
}
.forcewrap {
  word-break: break-all;
}
</style>