const axios = require("axios");
import { link } from "../secrets/sdkHandler";
import * as K from "../constants/constants";
import { sdkEncoder } from "../composables/cipherHandler";
import { issue_zid } from "../composables/sdkOperations"

export const readBalance = async(zid, targetAssets = [{ asset_code: K.gas, asset_issuer: 'native' }, { asset_code: K.asset, asset_issuer: issue_zid }]) => {
    const xid = sdkEncoder.zidToxid(zid);
    const url = `${link}/accounts/${xid}`;
    let balances = [];
    try {
        let response = await axios.get(url);
        let responseJson = response.data;
        if (responseJson.balances) {
            const records = responseJson.balances;
            records.forEach((record) => {
                let asset_code = K.gas;
                let asset_issuer = "native";
                let amount = record.balance;
                if (record.asset_type != 'native') {
                    asset_code = record.asset_code;
                    asset_issuer = sdkEncoder.xidTozid(record.asset_issuer)
                };
                const found = targetAssets.filter((e) => { return e.asset_code == asset_code && e.asset_issuer == asset_issuer });
                if (found.length == 1) {
                    balances.push({ asset_code, asset_issuer, amount });
                };
            });
        };
        return balances;
    } catch {
        return [];
    }
};