<template>
  <div class="mb-4 text-center">
    <AppButton text="Create Account 建立荷包" :action="createWallet" />

    <!-- <div v-if="!creating && K.devMode">
      <div>
        <AppButton text="Read Wallet" :action="readWallet" />
      </div>
      <AppButton text="~Clear Wallet~" :action="clearWallet" />
    </div> -->
  </div>
</template>

<script>
import { ref } from "vue";
import getUser from "../composables/getUser";
import {
  getPassword,
  sdkEncoder,
  makeHash,
  Encrypt,
  objB64,
  getMnemonic,
  getAccount,
} from "../composables/cipherHandler";
import {
  updateUser,
  storage,
  readWallet,
  wordFormatter,
  dialogHandler,
  notifyHandler,
} from "../composables/handlers";
import * as K from "../constants/constants";
import AppButton from "./AppButton.vue";
import { useRouter } from "vue-router";
import { transferPhrase } from "../secrets/secrets";
import { projectFirestore } from "../secrets/fbsecret";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup(props, context) {
    const router = useRouter();
    const { user } = getUser();
    const creating = ref(false);
    const mnemonic = ref("");

    const clearWallet = async () => {
      creating.value = true;
      if (confirm("Warning! Clear Wallet?")) {
        await updateUser("", "");
        await storage.remove("secure");
        context.emit("reload");
        router.replace({ name: "Account" });
      }
      creating.value = false;
    };

    const showMnemonic = async (mnemonic) => {
      const wordLength = mnemonic.split(" ").length;
      const message = `Write down your ${wordLength}-word Mnemonic:\n寫低你嘅${wordLength}字密䃙：\n\n${mnemonic}`;
      if (confirm(message)) {
        const place = Math.floor(Math.random() * 12);
        const item = [
          "1st",
          "2nd",
          "3rd",
          "4th",
          "5th",
          "6th",
          "7th",
          "8th",
          "9th",
          "10th",
          "11th",
          "12th",
        ];
        const target = mnemonic.split(" ")[place];
        dialogHandler({
          title: "Authentication 認証",
          message: `What is the ${
            item[place]
          } secret phrase of your mnemonic?<br>你第${place + 1}個密䃙係咩？`,
          html: true,
          prompt: {
            model: "",
            type: "text",
          },
          persistent: true,
          ok: {
            label: "OK 得",
            style: { "text-transform": "none" },
            color: "green",
          },
          cancel: {
            label: "Cancel 取消",
            style: { "text-transform": "none" },
          },
        })
          .onOk(async (inputMnemonic) => {
            inputMnemonic = wordFormatter(inputMnemonic);
            if (target == inputMnemonic) {
              const wallet = await readWallet();
              let secure = wallet.secure;
              secure["backedUp"] = true;
              const encrypted = await Encrypt(secure, wallet.pack.key);
              await storage.set("secure", encrypted);
              await router.replace({
                name: "Mnemonic",
                params: { mnemonic: mnemonic , backTo: "Account"},
              });
            } else {
              notifyHandler({
                message: "Invalid input!",
                color: "warning",
              });
              showMnemonic(mnemonic);
            }
          })
          .onCancel(() => {
            showMnemonic(mnemonic);
          });
      } else {
        showMnemonic(mnemonic);
      }
    };

    const createWallet = async () => {
      creating.value = true;
      const wallet = await readWallet();
      const username = wallet.disclose.username;
      const pin = wallet.pack.pin;
      if (wallet.disclose.zid && wallet.pack.zid) {
        K.print("wallet already exist");
      } else {
        const uid = user._value.uid;
        let salt = getPassword();
        let entropy = uid + salt + transferPhrase;
        const mnemonic = getMnemonic(12);
        const wallet = getAccount(mnemonic, entropy);

        const xid = wallet.xid;
        const seed = wallet.seed;

        const zid = sdkEncoder.xidTozid(xid);
        const zeed = sdkEncoder.xidTozid(seed);
        const key = makeHash.qh(await makeHash.hash(zeed + mnemonic + entropy));
        const cursor = "0";

        K.print(zid, zeed);

        const disclose = {
          zid,
          username,
          cursor,
        };

        const secure = {
          zeed,
          mnemonic,
          zid,
          attempts: 0,
          locked: false,
        };

        const pack = {
          zid,
          salt,
          key,
          pin,
        };

        K.print(disclose, secure);
        const encrypted = await Encrypt(secure, key);

        const name = objB64.encode(disclose);

        const url = objB64.encode(pack, true);

        try {
          // save to firestore
          await projectFirestore.collection("setup").doc(uid).set({
            zid,
            salt,
          });
          let updated = await updateUser(name, url);
          if (updated) {
            await storage.set("secure", encrypted);
            showMnemonic(mnemonic);
          };
        } catch (err) {
          K.error("storage issue", err.message);
        };
      };
      context.emit("reload");
      creating.value = false;
    };

    return {
      readWallet,
      createWallet,
      clearWallet,
      creating,
      mnemonic,
      K,
    };
  },
};
</script>

