// require('dotenv').config();
import { ref } from 'vue'
const { Base64 } = require('js-base64');

import * as K from "../constants/constants";

import * as SH from "../secrets/sdkHandler";

import { assetDefinition, aka } from '../secrets/secrets';
import { sdkEncoder, Encrypt, Decrypt } from './cipherHandler';
import { readWallet, storage, watchPayment, updateUserHandler } from "../composables/handlers";
import { ReadCursor } from "../composables/readCursor";


const sdk = SH.sdk;
const server = SH.newServer();

const accounts = ref([])
const records = ref({})

async function fetchAccount() {
    try {
        const wallet = await readWallet()
        let xid = sdkEncoder.zidToxid(wallet.disclose.zid);
        var pair = sdk.Keypair.fromPublicKey(
            xid,
        );
        const account = await server.loadAccount(pair.publicKey());
        const attr = account.data_attr;
        let data = [];
        Object.keys(attr).forEach(key => {
            const value = Base64.decode(attr[key]);
            const keyvalue = { key, value };
            data.push(keyvalue);
        });
        const count = account.subentry_count;
        let balances = [];
        let min_balance;
        let hasAsset = false;
        account.balances.forEach(function(balance) {
            let precision = 7;
            let alias = '';
            let limit = "unlimited";
            let code = balance.asset_code;
            let issuer = "native";
            let nft = true;
            if (balance.asset_type == "native") {
                code = K.gas;
                nft = false;
                precision = 5;
            } else {
                issuer = sdkEncoder.xidTozid(balance.asset_issuer);
                limit = balance.limit;
            };
            if (K.asset == code && assetDefinition[K.asset].asset_issuer == issuer) {
                nft = false;
                precision = assetDefinition[code].precision;
                hasAsset = true;
                issuer = "FreeHK.io"
            };
            if (aka[code]) {
                alias = aka[code]
            };
            balances.push({
                asset_code: code,
                asset_issuer: issuer,
                amount: balance.balance,
                limit,
                nft,
                alias,
                precision,
            });
            if (balance.asset_type == "native") {
                min_balance = (1 + count * 0.5 + (sdk.BASE_FEE * 0.0000001)).toFixed(7);
            };
        });

        const output = { zid: sdkEncoder.xidTozid(account.id), min_balance, count, balances, data, hasAsset };
        K.print(output)

        accounts.value = output.balances;
        if (output) {
            const passkey = wallet.pack.key;
            let oldBalance = Decrypt(await storage.get('balances'), passkey);
            let oldAccount = Decrypt(await storage.get('account'), passkey);
            let listen = await storage.get("listen");
            if (listen) {
                await watchPayment();
            }
            if (oldBalance != JSON.stringify(output.balances) || oldAccount != JSON.stringify(output)) {
                const balanceEncrypted = await Encrypt(output.balances, passkey);
                await storage.set('balances', balanceEncrypted);
                const accountEncrypted = await Encrypt(output, passkey);
                await storage.set('account', accountEncrypted);
                const c = await ReadCursor(wallet.disclose.zid);
                const cursor = c.cursor;
                await updateUserHandler({ disclose: { cursor: cursor } });
                if (!listen) {
                    await watchPayment({ memo: wallet.disclose.zid });
                }
            }
        };
        records.value = output;
        return output;
    } catch (err) {
        K.error(err.message);
        return null
    };
};

const getAccount = () => {
    return { accounts, fetchAccount, records }
}

export default getAccount