<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">About</h4>
    <div>
      <p>Crinkle Co NFTs.</p>
    </div>
    <div>
      <AppButton text="Back" :action="toAccount" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const toAudit = () => {
      router.replace({ name: "Audit" });
    };

    const toTerms = async () => {
      let back = { name: "About" };
      await router.replace({
        name: "Article",
        params: { id: "terms", back: JSON.stringify(back) },
      });
    };

    return {
      toAccount,
    };
  },
};
</script>

