const axios = require("axios");
import { link } from "../secrets/sdkHandler";
import * as K from "../constants/constants";
import { sdkEncoder } from "../composables/cipherHandler";
import { issue_zid } from "../composables/sdkOperations"
const { Base64 } = require('js-base64');

export const readData = async(target = K.asset, zid = issue_zid) => {
    const xid = sdkEncoder.zidToxid(zid);
    const url = `${link}/accounts/${xid}`;
    try {
        let response = await axios.get(url);
        let responseJson = response.data;
        let data = await JSON.parse(Base64.decode(responseJson.data[target]));
        return data;
    } catch (err) {
        K.error(err.message);
        return null;
    };
};