const axios = require('axios');
import { edCoder, sdkEncoder } from "../composables/cipherHandler";
import * as K from "../constants/constants";
import { link } from '../secrets/sdkHandler';

export const ReadCursor = async(zid) => {
    const xid = sdkEncoder.zidToxid(zid);
    const url = `${link}/accounts/${xid}/transactions/?limit=1&order=desc&include_failed=false`;
    try {
        let response = await axios.get(url);
        if (response.data) {
            let responseJson = await response.data;
            const links = responseJson._links.prev.href;
            const cursor = links.split("cursor=")[1].split("&include_failed=false")[0].toString();
            const asset_code = edCoder.encode(cursor);
            const output = { cursor, asset_code };
            K.print(output);
            return output;
        }
    } catch (err) {
        K.print('cannot find account');
        return null;
    };
};