import { projectAuth, gprovider } from "../secrets/fbsecret";
import * as K from "../constants/constants";
import { ref } from "vue"

const error = ref(null)

const login = async() => {
    try {
        let result = await projectAuth
            .signInWithRedirect(gprovider)
            // .signInWithPopup(gprovider)
            // if (!result) {
            //   throw new Error('Could not complete the login')
            // }
            // var credential = result.credential;
            // var token = credential.accessToken;
            // var user = result.user;
        error.value = null
        return true
    } catch (err) {
        // var errorCode = err.code;
        var errorMessage = err.message;
        // var email = err.email;
        // var credential = err.credential;
        error.value = 'Login invalid'
        K.error(errorMessage);
        return false
    }
};

const useLogin = () => {
    return { error, login }
}


export default useLogin